import { getTodayTimestamp } from "../daily";

import { getKey, storeKey } from ".";

function getGamesData() {
  return getKey("games") || {};
}

export function getGameModeData(mode) {
  return getGamesData()[mode] || {};
}

export function saveNewGame(mode, wordToGuess, otherGameData = {}) {
  const gamesData = getGamesData();

  storeKey("games", {
    ...gamesData,
    [mode]: {
      wordToGuess,
      suggestedWords: [],
      lockedLetters: [],
      highlightedLetters: {},
      duration: 0,
      ...otherGameData,
    },
  });
}

export function giveUpGame(mode) {
  const gamesData = getGamesData();
  const gameModeData = getGameModeData(mode);

  storeKey("games", {
    ...gamesData,
    [mode]: {
      ...gameModeData,
      hasGivenUp: true,
    },
  });
}

export function deleteGameData(mode) {
  if (mode === "daily") {
    // Can't delete daily game!
    return;
  }

  const gamesData = { ...getGamesData() };
  delete gamesData[mode];

  storeKey("games", gamesData);
}

export function saveWordSuggestion(mode, word) {
  const gamesData = getGamesData();
  const gameModeData = getGameModeData(mode);

  const nextWords = gameModeData.suggestedWords
    ? Array.from(gameModeData.suggestedWords)
    : [];
  nextWords.push(word);

  storeKey("games", {
    ...gamesData,
    [mode]: {
      ...gameModeData,
      suggestedWords: nextWords,
    },
  });
}

export function saveDuration(mode, duration) {
  const gamesData = getGamesData();
  const gameModeData = getGameModeData(mode);

  storeKey("games", {
    ...gamesData,
    [mode]: {
      ...gameModeData,
      duration,
    },
  });
}

export function saveGameHighlightLetter(mode, letter, value) {
  const gamesData = getGamesData();
  const gameModeData = getGameModeData(mode);

  const highlightedLetters = { ...(gameModeData.highlightedLetters || {}) };

  storeKey("games", {
    ...gamesData,
    [mode]: {
      ...gameModeData,
      highlightedLetters: {
        ...highlightedLetters,
        [letter]: value,
      },
    },
  });
}

export function saveGameAllHighlightedLetters(mode, highlightedLetters) {
  const gamesData = getGamesData();
  const gameModeData = getGameModeData(mode);

  storeKey("games", {
    ...gamesData,
    [mode]: {
      ...gameModeData,
      highlightedLetters,
    },
  });
}

export function saveGameLockLetter(mode, letter) {
  const gamesData = getGamesData();
  const gameModeData = getGameModeData(mode);

  const lockedLetters = [...(gameModeData.lockedLetters || [])];

  storeKey("games", {
    ...gamesData,
    [mode]: {
      ...gameModeData,
      lockedLetters: [...lockedLetters, letter],
    },
  });
}

export function saveGameUnhighlightLetter(mode, letter) {
  const gamesData = getGamesData();
  const gameModeData = getGameModeData(mode);

  const highlightedLetters = { ...(gameModeData.highlightedLetters || {}) };
  delete highlightedLetters[letter];

  storeKey("games", {
    ...gamesData,
    [mode]: {
      ...gameModeData,
      highlightedLetters,
    },
  });
}

export function saveGameUnhighlightAllLetters(mode) {
  const gamesData = getGamesData();
  const gameModeData = getGameModeData(mode);

  storeKey("games", {
    ...gamesData,
    [mode]: {
      ...gameModeData,
      highlightedLetters: {},
    },
  });
}

// Daily specific

export function getTodayDailyData() {
  const dailyData = getGameModeData("daily");

  return dailyData.timestamp === getTodayTimestamp() ? dailyData : {};
}

export function saveNewDailyGame(wordToGuess, timestamp) {
  saveNewGame("daily", wordToGuess, { timestamp });
}

// Tuto specific

export function saveCurrentTutorialStep(step) {
  const gamesData = getGamesData();
  const gameModeData = getGameModeData("tuto");

  storeKey("games", {
    ...gamesData,
    tuto: {
      ...gameModeData,
      currentTutorialStep: step,
    },
  });
}
