import { useEffect, useState } from "react";
import Button from "../../common/Button";
import Logo from "../../common/Logo";
import SectionContainer from "../../common/SectionContainer";
import SlideUp from "../../common/SlideUp";

import { useMainMonster } from "../../monster/MonsterProvider";

export default function Welcome() {
  const [welcomeStep, setWelcomeStep] = useState(1);

  const { setMonsterState, showMonsterBubble, hideMonsterBubble } =
    useMainMonster();

  useEffect(() => {
    let onDocumentClick = null;

    switch (welcomeStep) {
      case 1:
        setMonsterState("hi");
        showMonsterBubble(
          <>
            Bonjour et bienvenue dans <strong>Motigma</strong>&nbsp;!
          </>,
          true
        );
        onDocumentClick = () => setWelcomeStep(2);
        break;

      case 2:
        setMonsterState("speaking");
        showMonsterBubble(
          <>
            Il semblerait que a soit votre premire fois ici.
            <br />
            Souhaitez-vous apprendre  jouer&nbsp;?
          </>,
          true
        );
        onDocumentClick = () => setWelcomeStep(3);
        break;

      case 3:
        setMonsterState("thinking");
        onDocumentClick = () => setWelcomeStep(3);
        break;
    }

    if (onDocumentClick) {
      document.body.addEventListener("click", onDocumentClick, { once: true });
    }

    return () => {
      hideMonsterBubble();
      setMonsterState("walking");

      if (onDocumentClick) {
        document.body.removeEventListener("click", onDocumentClick);
      }
    };
  }, [welcomeStep]);

  return (
    <SectionContainer>
      <Logo />

      {welcomeStep === 3 && (
        <>
          <SlideUp delay={50}>
            <Button
              as="a"
              variant="primary"
              href="#explanations"
              sx={{ mb: 4 }}
            >
              Voir le tuto
            </Button>
          </SlideUp>

          <SlideUp delay={100}>
            <Button as="a" size="small" variant="secondary" href="#title">
              Passer le tuto
            </Button>
          </SlideUp>
        </>
      )}
    </SectionContainer>
  );
}
