import wordsFileContent from "../../words.txt";

export function normalizeWord(word) {
  return word
    .trim()
    .toUpperCase()
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .replace(/[^a-zA-Z0-9\- ]/g, "");
}

const wordsData = wordsFileContent.split(/\n/);
const searchableWords = [];
const submitableWords = [];

wordsData.forEach((wordData) => {
  const [word, searchable] = wordData.split(":");
  const normalizedWord = normalizeWord(word);

  if (searchable === "1") {
    searchableWords.push(normalizedWord);
  }

  submitableWords.push(normalizedWord);
});

const numberOfLettersToSearchableWords = new Map();
const numberOfLettersToSubmitableWords = new Map();
const checkedWords = {};

function getRandomItem(arr) {
  return arr[Math.floor(Math.random() * arr.length)];
}

function hasDoubleLetters(word) {
  const seenLetters = {};

  for (let letter of word) {
    if (letter in seenLetters) {
      return true;
    }
    seenLetters[letter] = 1;
  }

  return false;
}

export function countCommonLetters(word1, word2) {
  const set1 = new Set(word1);
  const set2 = new Set(word2);
  let commonLettersCount = 0;

  set1.forEach((letter) => {
    if (set2.has(letter)) {
      commonLettersCount++;
    }
  });

  return commonLettersCount;
}

function getAllWordsWithNumberOfLetters(totalOfLetters, inSubmitableWords) {
  const key = String(totalOfLetters);

  const wordsToFilter = inSubmitableWords ? submitableWords : searchableWords;
  const mapCache = inSubmitableWords
    ? numberOfLettersToSubmitableWords
    : numberOfLettersToSearchableWords;

  if (!mapCache.has(key)) {
    mapCache.set(
      key,
      wordsToFilter.filter((w) => w.length === totalOfLetters)
    );
  }

  return mapCache.get(key);
}

export function isValidWord(word) {
  if (!(word in checkedWords)) {
    checkedWords[word] = getAllWordsWithNumberOfLetters(
      word.length,
      true
    ).includes(word);
  }

  return checkedWords[word];
}

export function getRandomWord(totalOfLetters) {
  const allWordWithThisNumberOfLetters = getAllWordsWithNumberOfLetters(
    totalOfLetters,
    false
  );

  const allWordWithThisNumberOfLettersWithoutDoubleLetters =
    allWordWithThisNumberOfLetters.filter((w) => !hasDoubleLetters(w));

  const word = getRandomItem(
    allWordWithThisNumberOfLettersWithoutDoubleLetters
  );
  return word;
}

const wordLengthFromDayIndex = [
  7, // Sunday
  4, // Monday
  5, // Tuesday
  6, // Wednesday
  5, // Thursday
  4, // Friday
  5, // Saturday
];

export function getWordFromSeed(seed) {
  const wordIndex = Math.floor(seed / 10);
  const lengthIndex = seed % 10;
  const wordLength = wordLengthFromDayIndex[lengthIndex] || 5;

  const allPossibleWords = searchableWords.filter((word) => {
    const length = word.length;

    return length === wordLength && !hasDoubleLetters(word);
  });

  return allPossibleWords[wordIndex % allPossibleWords.length];
}
