import { useCallback, Fragment } from "react";

import styled from "@mui/system/styled";

import GameProvider from "../../game/GameProvider";
import TutoProvider from "../../tuto/TutoProvider";
import { useCurrentSection } from "../SectionsProvider";

import Container from "./Container";
import ErrorMessage from "./ErrorMessage";
import Form from "./Form";
import Navigation from "./Navigation";

const Root = styled("section")(({ theme }) => ({
  margin: `${theme.spacing(1)} auto`,
  width: "100%",
}));

export default function Game() {
  const { currentSectionVars, goToSection } = useCurrentSection();
  const gameMode = currentSectionVars.get("mode");

  const onGameEnd = useCallback(
    () => goToSection("endGame", { mode: gameMode }),
    [gameMode, goToSection]
  );

  const GameWrapper = gameMode === "tuto" ? TutoProvider : Fragment;

  return (
    <GameProvider mode={gameMode} onGameEnd={onGameEnd}>
      <GameWrapper>
        <ErrorMessage />
        <Root>
          <Container>
            <Navigation />
          </Container>
          <Form />
        </Root>
      </GameWrapper>
    </GameProvider>
  );
}
