import styled from "@mui/system/styled";

import Input from "./Input";

const Root = styled(Input)(({ theme }) => {
  const sliderThumb = {
    appearance: "none",
    width: "1.4rem",
    height: "1.4rem",
    cursor: "pointer",
    transition: "0.5s",
  };

  const sliderThumbActive = {
    transform: "scale(1.3, 0.8)",
  };

  const bubble = {
    aspectRatio: "1 / 1",
    border: "0.1rem solid #fff",
    borderRadius: "50%",
    background: `
      radial-gradient(circle at 70% 30%, #fff, transparent 25%),
      radial-gradient(
        circle at 60% 55%,
        transparent 60%,
        rgba(255, 0, 255, 0.8) 100%
      ),
      radial-gradient(
        circle at 50% 50%,
        transparent 40%,
        rgba(0, 255, 255, 0.2) 60%,
        transparent 68%
      ),
      radial-gradient(
        circle at 50% 55%,
        transparent 35%,
        rgba(255, 255, 0, 0.2) 45%,
        transparent 55%
      )`,
  };

  return {
    appearance: "none",
    display: "block",
    width: "100%",
    verticalAlign: "middle",
    maxWidth: "none",
    boxSizing: "border-box",

    "&::-webkit-slider-thumb": {
      ...sliderThumb,
      transitionTimingFunction: "cubic-bezier(0.73, 2, 0.25, -0.7)",
      ...bubble,
    },

    "&::-moz-range-thumb": {
      ...sliderThumb,
      ...bubble,
    },

    "&:active::-webkit-slider-thumb": {
      transitionTimingFunction: "linear",
      ...sliderThumbActive,
    },

    "&:active::-moz-range-thumb": {
      animation: "none",
      ...sliderThumbActive,
    },
  };
});

export default function Range(props) {
  return <Root {...props} type="range" />;
}
