import Box from "@mui/system/Box";
import Stack from "@mui/system/Stack";

import Button from "../../common/Button";
import DownloadButton from "../../common/DownloadButton";
import Panel from "../../common/Panel";
import SectionContainer from "../../common/SectionContainer";
import SlideUp from "../../common/SlideUp";

import { useCurrentSection } from "../SectionsProvider";

export default function StartGame() {
  const { currentSectionVars, goToSection } = useCurrentSection();
  const gameMode = currentSectionVars.get("mode");

  if (gameMode) {
    goToSection("game", { mode: gameMode });
    return null;
  }

  return (
    <Box my="auto">
      <SectionContainer>
        <Box mb={4}>
          <Panel title="Choix du mode" />
        </Box>

        <Stack
          flexDirection={process.env.IS_SITE ? "column-reverse" : "column"}
          alignItems="center"
          gap={2}
        >
          <SlideUp delay={100}>
            <Button
              as="a"
              variant="secondary"
              size="small"
              href="#game?mode=easy"
              disabled={process.env.IS_SITE}
            >
              Facile (4 lettres)
            </Button>
          </SlideUp>

          <SlideUp delay={150}>
            <Button
              as="a"
              variant="primary"
              size="small"
              href="#game?mode=normal"
              disabled={process.env.IS_SITE}
            >
              Normal (5 lettres)
            </Button>
          </SlideUp>

          <SlideUp delay={200}>
            <Button
              as="a"
              variant="danger"
              size="small"
              href="#game?mode=hard"
              disabled={process.env.IS_SITE}
            >
              Difficile (6 lettres)
            </Button>
          </SlideUp>

          <SlideUp delay={225}>
            {process.env.IS_SITE && (
              <Panel>
                Les modes ci-dessous sont disponibles dans la version complte
                du jeu.
                <br />
                <br />
                <div style={{ textAlign: "center" }}>
                  <DownloadButton scale={0.5} />
                </div>
              </Panel>
            )}
          </SlideUp>

          <br />

          <SlideUp delay={250}>
            <Button
              as="a"
              variant="tertiary"
              size="small"
              href="#game?mode=daily"
            >
              Mot du jour
            </Button>
          </SlideUp>

          <br />

          <SlideUp delay={275}>
            <Button as="a" variant="primary" size="very-small" href="#title">
              Retour
            </Button>
          </SlideUp>
        </Stack>
      </SectionContainer>
    </Box>
  );
}
