import {
  useState,
  createContext,
  useContext,
  useEffect,
  useCallback,
} from "react";
import Achievement from "./Achievement";

import { useOptions } from "../options/OptionsProvider";
import { unlockAchievement } from "../save/achievements";
import { useSounds } from "../sounds/SoundsProvider";

const AchievementsContext = createContext({});

export default function AchievementsProvider({ children }) {
  const [newAchievements, setNewAchievements] = useState([]);
  const { playSound } = useSounds();
  const { options } = useOptions();

  const showAchievement = useCallback((achievementId) => {
    if (unlockAchievement(achievementId)) {
      playSound("achievement");
      setNewAchievements((prev) => [achievementId, ...prev]);
    }
  }, []);

  const hideAchievement = (achievementId) => {
    setNewAchievements((prev) => prev.filter((id) => id !== achievementId));
  };

  useEffect(() => {
    if (options.backgroundBrightness === 0.1) {
      showAchievement("no-light");
    }
  }, [options.backgroundBrightness, showAchievement]);

  return (
    <AchievementsContext.Provider value={{ newAchievements, showAchievement }}>
      {children}
      {newAchievements.map((achievementId, index) => (
        <Achievement
          key={achievementId}
          id={achievementId}
          popoverTimeout={10000}
          onPopoverEnd={() => hideAchievement(achievementId)}
          style={{
            "--y": `${index * 75}px`,
          }}
        />
      ))}
    </AchievementsContext.Provider>
  );
}

export function useAchievements() {
  return useContext(AchievementsContext);
}
