import { useState, createContext, useContext, useCallback } from "react";

const MonsterContext = createContext({
  monsterState: "",
  setMonsterState() {},
});

export default function MonsterProvider({ children }) {
  const [monsterState, setMonsterState] = useState("walking");

  const [monsterBubble, setMonsterBubble] = useState(null);

  const hideMonsterBubble = useCallback(() => setMonsterBubble(null), []);
  const showMonsterBubble = useCallback(
    (message, showClickToContinue = false) =>
      setMonsterBubble({
        message,
        showClickToContinue,
      }),
    []
  );

  const setState = useCallback((newState) => {
    setMonsterState((prev) => {
      if (
        newState.includes("walking") &&
        !prev.includes("walking") &&
        !prev.includes("hidden")
      ) {
        return `${newState} from-middle`;
      }

      return newState;
    });
  }, []);

  return (
    <MonsterContext.Provider
      value={{
        monsterState,
        setMonsterState: setState,
        monsterBubble,
        showMonsterBubble,
        hideMonsterBubble,
      }}
    >
      {children}
    </MonsterContext.Provider>
  );
}

export function useMainMonster() {
  return useContext(MonsterContext);
}
