import { useState, useEffect, useCallback } from "react";
import { flushSync } from "react-dom";

import Stack from "@mui/system/Stack";

import { normalizeWord } from "../../words";

import Button from "../../common/Button";
import SlideUp from "../../common/SlideUp";

import { useAchievements } from "../../achievements/AchievementsProvider";
import { useGameContext } from "../../game/GameProvider";
import { hasLockedLetters } from "../../game/gameStateSelectors";
import { useMainMonster } from "../../monster/MonsterProvider";
import { useSounds } from "../../sounds/SoundsProvider";
import { useTutorial } from "../../tuto/TutoProvider";

import { countCommonLetters, isValidWord } from "../../words";

import AbovePanel from "./AbovePanel";
import Alphabet from "./Alphabet";
import Container from "./Container";
import ShuffleLetters from "./ShuffleLetters";

export default function Form() {
  const { gameState, dispatch } = useGameContext();
  const { playSound } = useSounds();
  const { showAchievement } = useAchievements();
  const { setMonsterState, showMonsterBubble, hideMonsterBubble } =
    useMainMonster();

  const { expectedWord = "", disabledInteractions } = useTutorial();

  const letterCount = gameState.wordToGuess.length;
  const suggestionCount = gameState.suggestedWords.length;
  const highlightedLetters = Object.entries(gameState.highlightedLetters);
  const highlightedLetterCount = highlightedLetters.length;
  const greenLetters = highlightedLetters
    .filter(([, value]) => value === 1)
    .map(([letter]) => letter);

  const greenLetterCount = greenLetters.length;
  const [areGreenLettersHighlighted, setAreGreenLettersHighlighted] = useState(
    hasLockedLetters(gameState)
  );

  const [suggestedWord, setSuggestedWord] = useState("");

  const [isSpeakingAboutAnagram, setIsSpeakingAboutAnagram] = useState(false);

  const storeWord = (newWord) => {
    if (disabledInteractions.suggestWord) {
      return;
    }

    setSuggestedWord(normalizeWord(newWord));
  };

  const submitWord = useCallback(() => {
    if (isSpeakingAboutAnagram) {
      hideMonsterBubble();
      setMonsterState("walking from-middle");
      setIsSpeakingAboutAnagram(false);
    }

    if (disabledInteractions.suggestWord) {
      return;
    }

    if (!suggestedWord) {
      dispatch({
        type: "suggestNonValidWord",
        payload: { message: "Vous n'avez pas propos de mot !" },
      });

      return;
    }

    if (expectedWord && suggestedWord !== expectedWord) {
      dispatch({
        type: "suggestNonValidWord",
        payload: {
          message: `Mais... Ce n'est pas le mot <b>${expectedWord.toUpperCase()}</b> !`,
        },
      });

      return;
    }

    if (suggestedWord === "MOTIGMA") {
      dispatch({
        type: "suggestNonValidWord",
        payload: {
          message: `Mais... "Motigma" c'est le nom de ce jeu ! Vous avez conscience que ce n'est pas un vrai mot, n'est-ce pas... ?`,
        },
      });
      showAchievement("motigma");

      return;
    }

    if (suggestedWord.length !== letterCount) {
      dispatch({
        type: "suggestNonValidWord",
        payload: {
          message: `Ce mot ne possde pas ${letterCount} lettres.`,
        },
      });
      showAchievement("how-many-letters");

      return;
    }

    const isValid = isValidWord(suggestedWord);

    const commonLettersCount = isValid
      ? countCommonLetters(gameState.wordToGuess, suggestedWord)
      : "?";

    if (
      commonLettersCount === letterCount &&
      suggestedWord !== gameState.wordToGuess
    ) {
      dispatch({ type: "highlightAllLetters" });

      window.requestAnimationFrame(() => {
        document.startViewTransition(() => {
          flushSync(() => {
            setAreGreenLettersHighlighted(true);
          });
        });
      });

      if (
        gameState.suggestedWords.filter((word) => word === suggestedWord)
          .length === 0
      ) {
        const lockedLettersCount = gameState.lockedLetters?.length || 0;

        let monsterMessage = "";

        if (lockedLettersCount === 0) {
          monsterMessage = (
            <p>
              Bravo ! Vous avez trouv un <b>anagramme</b>, vous y tes presque
              !<br />
              Pour vous aider, la lettre{" "}
              <kbd>{gameState.wordToGuess[lockedLettersCount]}</kbd> a t
              marque en bleue : cela signifie qu'elle est  la bonne place !
            </p>
          );
        } else {
          monsterMessage = (
            <p>
              Wow, vous avez <b>encore</b> trouv un anagramme !<br />
              Cette fois, la lettre{" "}
              <kbd>{gameState.wordToGuess[lockedLettersCount]}</kbd> a t
              marque en bleue. Vous tes  "a" de trouver le mot-mystre !
            </p>
          );
        }

        showMonsterBubble(monsterMessage);
        setMonsterState("happy");
        setIsSpeakingAboutAnagram(true);

        window.requestAnimationFrame(() => {
          document.getElementById("app").scrollTo({
            top: document.body.scrollHeight,
            behavior: "smooth",
          });
        });

        dispatch({ type: "lockRandomLetter" });
      }
    }

    dispatch({
      type: "suggestWord",
      payload: { word: suggestedWord },
    });

    setSuggestedWord("");
  }, [isSpeakingAboutAnagram, expectedWord, letterCount, suggestedWord]);

  const addLetter = useCallback(
    (letter) => {
      playSound("click");
      storeWord(
        suggestedWord.length < letterCount
          ? suggestedWord + letter
          : suggestedWord
      );
    },
    [suggestedWord, letterCount]
  );

  const eraseLastLetter = useCallback(() => {
    playSound("click");
    setSuggestedWord((prev) => {
      const currentSuggestedWordLength = prev.length;

      return currentSuggestedWordLength > 1
        ? prev.substring(0, currentSuggestedWordLength - 1)
        : "";
    });
  }, []);

  useEffect(() => {
    if (!process.env.IS_SITE) {
      return;
    }

    const onKeyDown = (e) => {
      const key = e.key;

      if (/^[a-zA-Z]$/.test(key)) {
        addLetter(key.toUpperCase());
        e.preventDefault();
      } else if (key === "Backspace") {
        eraseLastLetter();
        e.preventDefault();
      } else if (key === "Enter") {
        submitWord();
        e.preventDefault();
      }
    };

    window.document.addEventListener("keydown", onKeyDown);

    return () => {
      window.document.removeEventListener("keydown", onKeyDown);
    };
  }, [process.env.IS_SITE, addLetter, eraseLastLetter, submitWord]);

  return (
    <form
      onSubmit={(e) => {
        e.preventDefault();
        submitWord();
      }}
    >
      <Container>
        <AbovePanel
          suggestedWord={suggestedWord}
          onWordChange={storeWord}
          disableField={disabledInteractions.suggestWord}
        />
      </Container>

      {!areGreenLettersHighlighted && (
        <Alphabet
          onLetterAdd={addLetter}
          onErase={eraseLastLetter}
          disableDrag={disabledInteractions.dragKeys}
        />
      )}

      <Stack mt={3} justifyContent="center" alignItems="center" gap={2}>
        {greenLetterCount > 2 && (
          <ShuffleLetters
            key={
              areGreenLettersHighlighted
                ? greenLetters.toString() + gameState.lockedLetters?.toString()
                : "none"
            }
            onActiveChange={(isActive) => {
              playSound("click");
              document.startViewTransition(() => {
                flushSync(() => {
                  setAreGreenLettersHighlighted(isActive);
                });
              });
            }}
            letters={areGreenLettersHighlighted ? greenLetters : []}
            onLetterAdd={addLetter}
            onErase={eraseLastLetter}
          />
        )}

        {highlightedLetterCount > 0 && !areGreenLettersHighlighted && (
          <SlideUp>
            <Button
              variant="secondary"
              size="very-small"
              type="button"
              onClick={() => dispatch({ type: "reinitLetters" })}
            >
              Rinitialiser les lettres
            </Button>
          </SlideUp>
        )}

        {suggestionCount > 2 && gameState.gameMode !== "tuto" && (
          <SlideUp>
            <Button
              component="a"
              href={`#giveUp?mode=${gameState.gameMode}`}
              variant="danger"
              size="very-small"
              sx={{ viewTransitionName: "give-up-button" }}
            >
              Abandonner
            </Button>
          </SlideUp>
        )}
      </Stack>
    </form>
  );
}
