import { useState } from "react";

import Box from "@mui/system/Box";
import Stack from "@mui/system/Stack";
import styled from "@mui/system/styled";

import { useAchievements } from "../../achievements/AchievementsProvider";

import ConstraintButton from "../../common/ConstraintButton";
import List from "../../common/List";
import Panel from "../../common/Panel";
import SectionContainer from "../../common/SectionContainer";
import { hasUnlockedAchievement } from "../../save/achievements";
import { useSounds } from "../../sounds/SoundsProvider";

import { useCurrentSection } from "../SectionsProvider";
import versions from "../../releaseNotes/versions";

const Root = styled("div")(({ theme }) => ({
  margin: "auto",
  width: `calc(100% - ${theme.spacing(1)})`,
  maxWidth: 768,
  boxSizing: "border-box",
}));

export default function ReleaseNotes() {
  const vars = useCurrentSection().currentSectionVars;
  const gameMode = vars.get("mode");

  const { showAchievement } = useAchievements();
  const { playSound } = useSounds();

  const [hasClickedOnRock, setHasClickedOnRock] = useState(false);
  const hasUnlockRockSecret = hasUnlockedAchievement("release-notes");

  return (
    <Root>
      <SectionContainer>
        <Stack mx={1} my={5} mb={10} gap={3}>
          <Panel
            title="Notes de versions"
            sx={{ textAlign: "center", alignSelf: "center" }}
          />

          {versions.map(({ name, notes }) => (
            <Panel
              title={<Box sx={{ textAlign: "center" }}>{name}</Box>}
              key={name}
            >
              <List>
                {typeof notes === "function"
                  ? notes({
                      isRockActive: hasClickedOnRock,
                      onRockClick: hasUnlockRockSecret
                        ? undefined
                        : () => {
                            setHasClickedOnRock(true);
                            playSound("rockIn");

                            window.setTimeout(() => {
                              playSound("rockOut");
                            }, 1500);
                          },
                      onRockExploded: hasUnlockRockSecret
                        ? undefined
                        : () => {
                            showAchievement("release-notes");
                          },
                    })
                  : notes}
              </List>
            </Panel>
          ))}

          <ConstraintButton
            component="a"
            variant="primary"
            size="small"
            href={gameMode ? `#options?mode=${gameMode}` : "#options"}
          >
            Retour
          </ConstraintButton>
        </Stack>
      </SectionContainer>
    </Root>
  );
}
