import { useEffect, useState } from "react";
import styled from "@mui/system/styled";

import Bubble from "../../monster/Bubble";
import Monster from "../../monster/Monster";
import { useMainMonster } from "../../monster/MonsterProvider";

import { useGameContext } from "../../game/GameProvider";

const Root = styled("div")(({ theme }) => ({
  position: "fixed",
  pointerEvents: "none",
  zIndex: theme.zIndex.errorMessage,
  left: 0,
  right: 0,
  top: 0,
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  gap: 32,
}));

const ErrorMonster = styled(Monster)(() => ({
  rotate: "180deg",
  transformOrigin: "center",
}));

const ErrorBubble = styled(Bubble)(({ theme }) => ({
  margin: theme.spacing(0, 1),
}));

export default function ErrorMessage() {
  const {
    monsterState,
    setMonsterState,
    monsterBubble,
    hideMonsterBubble,
    showMonsterBubble,
  } = useMainMonster();
  const [previousMonsterState, setPreviousMonsterState] = useState(null);
  const [previousMonsterBubble, setPreviousMonsterBubble] = useState(null);

  const { gameState, dispatch } = useGameContext();

  const hasFormError = Boolean(gameState.formError);

  useEffect(() => {
    if (hasFormError) {
      if (monsterState !== "hidden") {
        setPreviousMonsterState(monsterState);
        setMonsterState("hidden");
        setPreviousMonsterBubble(monsterBubble);
        hideMonsterBubble();
      }
    } else {
      if (previousMonsterState) {
        setMonsterState(previousMonsterState);
        setPreviousMonsterState(null);
      }

      if (previousMonsterBubble) {
        showMonsterBubble(
          previousMonsterBubble.message,
          previousMonsterBubble.showClickToContinue
        );
        setPreviousMonsterBubble(null);
      }
    }
  }, [
    previousMonsterState,
    monsterState,
    setPreviousMonsterState,
    hasFormError,
  ]);

  useEffect(() => {
    const resetFormError = () => {
      if (hasFormError) {
        dispatch({ type: "resetFormError" });
      }
    };

    document.body.addEventListener("pointerdown", resetFormError);

    return () =>
      document.body.removeEventListener("pointerdown", resetFormError);
  }, [hasFormError]);

  return hasFormError ? (
    <Root>
      <ErrorMonster state="nope" />
      <ErrorBubble
        arrowPosition="top"
        dangerouslySetInnerHTML={{ __html: gameState.formError }}
      />
    </Root>
  ) : null;
}
