import Button from "../../common/Button";

function wordCountHistoryValueToEmoji(value) {
  if (value === "?") {
    return "";
  }

  return ["0", "1", "2", "3", "4", "5", "6", "7"][value] || "";
}

export default function ShareButton({
  hasWon,
  tryCount,
  wordCountHistory,
  duration,
}) {
  const share = () => {
    let textToShare = "";

    if (hasWon) {
      if (tryCount === 1) {
        textToShare =
          "Et je l'ai trouv du PREMIER coup ! Peux-tu faire aussi bien que moi ?";
      } else {
        textToShare = `Cela m'a pris ${tryCount} essai${
          tryCount > 1 ? "s" : ""
        } : peux-tu faire mieux que moi ?`;
      }
    } else {
      textToShare = `J'ai d abandonner aprs ${tryCount} essais... Peux-tu faire mieux que moi ?`;
    }

    if (tryCount > 1) {
      textToShare += `\nMes essais : ${wordCountHistory
        .map(wordCountHistoryValueToEmoji)
        .join(" ")}`;
    }

    textToShare += `\nMon temps : ${duration}.`;

    const titleToShare = hasWon
      ? "J'ai trouv le mot du jour de Motigma !"
      : "Le mot du jour de Motigma est difficile !";

    const finalContentToShare = `${titleToShare}\n${textToShare}`;

    if (window.hasOwnProperty("cordova")) {
      navigator.share(finalContentToShare);
    } else {
      navigator.share({
        text: finalContentToShare,
      });
    }
  };

  return (
    <Button variant="secondary" size="small" onClick={share}>
      Partager
    </Button>
  );
}
