import { useEffect } from "react";

import styled from "@mui/system/styled";

import ConstraintButton from "../../common/ConstraintButton";
import DownloadButton from "../../common/DownloadButton";
import Logo from "../../common/Logo";
import SectionContainer from "../../common/SectionContainer";

import { useMainMonster } from "../../monster/MonsterProvider";
import Panel from "../../common/Panel";

const MainLogo = styled(Logo)(({ theme }) => ({
  width: "70dvw",
  height: "50dvh",
  marginTop: theme.spacing(1),
  marginBottom: theme.spacing(4),
}));

export default function Splaschreen() {
  const { setMonsterState, showMonsterBubble, hideMonsterBubble } =
    useMainMonster();

  useEffect(() => {
    setMonsterState("hi");
    showMonsterBubble(
      <>
        Bienvenue  <b>Motigma</b>&nbsp;!
      </>
    );

    return () => {
      setMonsterState("walking");
      hideMonsterBubble();
    };
  }, [setMonsterState, showMonsterBubble, hideMonsterBubble]);

  return (
    <SectionContainer>
      <MainLogo />

      <Panel sx={{ maxWidth: 500 }}>
        <p>
          Bienvenue  <b>Motigma</b> !<br />
          <br />
          <b>Motigma</b> est un jeu de lettres dans lequel vous devez trouver un
          mot-mystre en un minimum de coups.
          <br />
          <br />
          Pour cela, vous devez proposer des mots de la mme longueur que le
          mot-mystre, et le jeu vous indique combien de lettres sont communes
          entre votre proposition et le mot-mystre.
          <br />
          <br />
          Ce site vous prsente une version simplifie du jeu, pour vous
          permettre de le <b>tester gratuitement</b>.
          <br />
          <br />
          La version complte de <b>Motigma</b> est disponible sur Android pour
          seulement <b>0.99</b>&nbsp;!
        </p>
      </Panel>

      <DownloadButton sx={{ viewTransitionName: "main-download" }} />
      <br />
      <ConstraintButton
        href={`#title`}
        size="small"
        sx={{ viewTransitionName: "main-button" }}
      >
        Tester gratuitement
      </ConstraintButton>

      <br />
    </SectionContainer>
  );
}
