import { createContext, useContext, useCallback, useEffect } from "react";

import { useOptions } from "../options/OptionsProvider";

import { playMusic, stopMusic, playSound, init, setMusicVolume } from ".";

const SoundsContext = createContext({});

export default function SoundsProvider({ children }) {
  const { options } = useOptions();

  const initBackgroundMusic = async (initialVolume = 0) => {
    await init();
    setMusicVolume(initialVolume);
  };

  const playSoundAccordingToOption = useCallback(
    (soundId) => {
      if (options.sounds <= 0) {
        return;
      }

      playSound(soundId, options.sounds);
    },
    [options.sounds]
  );

  useEffect(() => {
    document.addEventListener("pause", stopMusic, false);
    document.addEventListener("resume", playMusic, false);

    return () => {
      document.removeEventListener("pause", stopMusic, false);
      document.removeEventListener("resume", playMusic, false);
    };
  }, []);

  return (
    <SoundsContext.Provider
      value={{
        initBackgroundMusic,
        playMusic,
        stopMusic,
        playSound: playSoundAccordingToOption,
      }}
    >
      {children}
    </SoundsContext.Provider>
  );
}

export function useSounds() {
  return useContext(SoundsContext);
}
