import { useReducer, createContext, useContext } from "react";

import { getOptions } from "../save/options";

import optionsReducer from "./optionsReducer";

const OptionsContext = createContext({});

const initOptions = ({ defaultKeyboardLayout }) => {
  const options = getOptions();

  if (!options.keyboardLayout) {
    options.keyboardLayout = defaultKeyboardLayout;
  }

  if (!options.keyboardActionsKeys) {
    options.keyboardActionsKeys = "delete-submit";
  }

  if (!options.keyboardActionsColor) {
    options.keyboardActionsColor = "grayscale(1) brightness(1.75) invert(1)";
  }

  if (!options.keyboardLettersColor) {
    options.keyboardLettersColor = "grayscale(1) brightness(1.5)";
  }

  if (!options.monsterColor) {
    options.monsterColor = "yellow";
  }

  if (!options.showTimer) {
    options.showTimer = "yes";
  }

  return options;
};

export default function OptionsProvider({ children, defaultKeyboardLayout }) {
  const [options, dispatch] = useReducer(
    optionsReducer,
    { defaultKeyboardLayout },
    initOptions
  );

  return (
    <OptionsContext.Provider value={{ options, dispatch }}>
      {children}
    </OptionsContext.Provider>
  );
}

export function useOptions() {
  return useContext(OptionsContext);
}
