import SectionsProvider from "./SectionsProvider";

import Achievements from "./achievements";
import EndGame from "./endGame";
import Explanations from "./explanations";
import Game from "./game";
import GiveUp from "./giveUp";
import News from "./news";
import Options from "./options";
import Policies from "./policies";
import ReleaseNotes from "./releaseNotes";
import SplashScreenApp from "./splashscreen/SplashScreenApp";
import SplashScreenSite from "./splashscreen/SplashScreenSite";
import StartGame from "./startGame";
import Title from "./title";
import Welcome from "./welcome";

export default function Sections({ sectionWrapper }) {
  const shouldGoToPolicies =
    process.env.IS_SITE && window.location.hash === "#policies";

  return (
    <SectionsProvider
      scrollContainer={document.getElementById("app")}
      defaultSection={shouldGoToPolicies ? "policies" : "splashscreen"}
      wrapper={sectionWrapper}
      sections={[
        {
          name: "splashscreen",
          element: process.env.IS_SITE ? (
            <SplashScreenSite />
          ) : (
            <SplashScreenApp />
          ),
          possibleNextSections: ["welcome", "title", "news"],
          enableViewTransitions: true,
        },
        {
          name: "welcome",
          element: <Welcome />,
          possibleNextSections: ["explanations", "title"],
        },
        {
          name: "explanations",
          element: <Explanations />,
          possibleNextSections: ["welcome", "title", "game"],
        },
        {
          name: "news",
          element: <News />,
          possibleNextSections: ["title"],
          enableViewTransitions: true,
        },
        {
          name: "title",
          element: <Title />,
          possibleNextSections: [
            "startGame",
            "game",
            "options",
            "achievements",
            "explanations",
            "policies",
          ],
        },
        {
          name: "policies",
          element: <Policies />,
          possibleNextSections: ["splashscreen", "title"],
        },
        {
          name: "achievements",
          element: <Achievements />,
          possibleNextSections: ["title", "options"],
        },
        {
          name: "startGame",
          element: <StartGame />,
          possibleNextSections: ["title", "game"],
        },
        {
          name: "game",
          element: <Game />,
          possibleNextSections: [
            "startGame",
            "title",
            "endGame",
            "options",
            "giveUp",
          ],
        },
        {
          name: "giveUp",
          element: <GiveUp />,
          possibleNextSections: ["game", "endGame"],
        },
        {
          name: "endGame",
          element: <EndGame />,
          possibleNextSections: ["title", "startGame"],
        },
        {
          name: "options",
          element: <Options />,
          possibleNextSections: [
            "game",
            "title",
            "releaseNotes",
            "achievements",
          ],
        },
        {
          name: "releaseNotes",
          element: <ReleaseNotes />,
          possibleNextSections: ["options"],
        },
      ]}
    />
  );
}
