import { useEffect } from "react";

import styled from "@mui/system/styled";
import Box from "@mui/system/Box";
import Stack from "@mui/system/Stack";

import Input from "../../common/form/Input";
import { formatDuration } from "../../common/time";
import Panel, { Content as PanelContent } from "../../common/Panel";
import Shake from "../../common/Shake";

import { useGameContext } from "../../game/GameProvider";
import { getValidTryCount } from "../../game/gameStateSelectors";

import { useOptions } from "../../options/OptionsProvider";
import { useSounds } from "../../sounds/SoundsProvider";
import WordList from "./WordList";

const Root = styled(Panel)(({ theme }) => ({
  marginBottom: theme.spacing(1),
  padding: theme.spacing(2),
  position: "relative",
  zIndex: theme.zIndex.neutral,
  boxSizing: "border-box",
  textAlign: "center",
  display: "block",
}));

const AbovePanelContent = styled(PanelContent)(() => ({
  padding: 0,
}));

const WordLetterCount = styled("div")({
  boxSizing: "content-box",
  width: "70%",
  maxWidth: "10rem",
  margin: "auto",
  textAlign: "right",
  bottom: "0.9rem",
  position: "relative",
  fontSize: "0.8rem",
  opacity: 0.5,
  letterSpacing: 1,
});

const GameData = styled("p")(({ theme }) => ({
  margin: theme.spacing(1),
  fontStyle: "italic",
  whiteSpace: "nowrap",
}));

export default function AbovePanel({
  suggestedWord,
  onWordChange,
  disableField = false,
}) {
  const { gameState } = useGameContext();
  const { playSound } = useSounds();
  const { options } = useOptions();
  const tryCount = getValidTryCount(gameState);

  const hasError = Boolean(gameState.formError);

  const letterCount = gameState.wordToGuess.length;

  useEffect(() => {
    if (hasError) {
      playSound("error");
    }
  }, [playSound, hasError]);

  return (
    <Root
      title={<>Trouvez le mot&nbsp;!</>}
      contentComponent={AbovePanelContent}
    >
      <p>
        Il s'agit d'un mot de{" "}
        <b style={{ fontSize: "1.5rem" }}>{letterCount}</b> lettres{" "}
        <u style={{ whiteSpace: "nowrap" }}>toutes diffrentes</u>.
      </p>

      <WordList gameState={gameState} />

      <Shake isActive={hasError}>
        <Input
          value={suggestedWord}
          onChange={(e) => onWordChange(e.target.value.trim())}
          type="text"
          autoComplete="off"
          autoCorrect="off"
          autoCapitalize="off"
          spellCheck="false"
          maxLength={letterCount}
          disabled={disableField}
        />
        <WordLetterCount>
          {suggestedWord.length}/{letterCount}
        </WordLetterCount>
      </Shake>

      <Stack
        flexDirection="row"
        justifyContent="space-between"
        alignItems="center"
      >
        {options.showTimer === "yes" && (
          <Box sx={{ marginRight: "auto" }}>
            <GameData>Temps : {formatDuration(gameState.duration)}</GameData>
          </Box>
        )}

        {tryCount > 0 && (
          <Box sx={{ marginLeft: "auto" }}>
            <GameData>
              Tentative{tryCount > 1 && "s"} : {tryCount}
            </GameData>
          </Box>
        )}
      </Stack>
    </Root>
  );
}
