import { saveOption } from "../save/options";
import { setMusicVolume, playSound } from "../sounds";

export default function optionsReducer(state, action) {
  switch (action.type) {
    case "setMusicVolume": {
      const newVolume = Number(action.payload.value);

      saveOption("music", newVolume);

      setMusicVolume(newVolume);

      return {
        ...state,
        music: newVolume,
      };
    }

    case "setSoundsVolume": {
      const newVolume = Number(action.payload.value);

      saveOption("sounds", newVolume);

      playSound("checkSound", newVolume);

      return {
        ...state,
        sounds: newVolume,
      };
    }

    case "setBackgroundEffect": {
      const newEffect = action.payload.value;

      saveOption("backgroundEffectName", newEffect);

      return {
        ...state,
        backgroundEffectName: newEffect,
      };
    }

    case "setBackgroundBrightness": {
      const newBrightness = Number(action.payload.value);

      saveOption("backgroundBrightness", newBrightness);

      return {
        ...state,
        backgroundBrightness: newBrightness,
      };
    }

    case "setKeyboardLayout": {
      const newLayout = action.payload.value;

      saveOption("keyboardLayout", newLayout);

      return {
        ...state,
        keyboardLayout: newLayout,
      };
    }

    case "setKeyboardActionsKeys": {
      const newLayout = action.payload.value;

      saveOption("keyboardActionsKeys", newLayout);

      return {
        ...state,
        keyboardActionsKeys: newLayout,
      };
    }

    case "setKeyboardActionsColor": {
      const newColor = action.payload.value;

      saveOption("keyboardActionsColor", newColor);

      return {
        ...state,
        keyboardActionsColor: newColor,
      };
    }

    case "setKeyboardLettersColor": {
      const newColor = action.payload.value;

      saveOption("keyboardLettersColor", newColor);

      return {
        ...state,
        keyboardLettersColor: newColor,
      };
    }

    case "setMonsterColor": {
      const newColor = action.payload.value;

      saveOption("monsterColor", newColor);

      return {
        ...state,
        monsterColor: newColor,
      };
    }

    case "setShowTimer": {
      const newTimerValue = action.payload.value;

      saveOption("showTimer", newTimerValue);

      return {
        ...state,
        showTimer: newTimerValue,
      };
    }

    default:
      return state;
  }
}
