import {
  createContext,
  useContext,
  useEffect,
  useCallback,
  useRef,
} from "react";

const DraggableContext = createContext({});

export default function DraggableProvider({
  children,
  onDragMove,
  onDragEnd,
  isDisabled,
}) {
  const longTouchClock = useRef(null);

  const dragEndHandler = useCallback(() => {
    if (longTouchClock.current) {
      window.clearTimeout(longTouchClock.current);
    }

    if (!isDisabled && onDragEnd) {
      onDragEnd();
    }
  }, [isDisabled, onDragEnd]);

  useEffect(() => {
    document.body.addEventListener(
      "pointerup",
      isDisabled ? null : dragEndHandler
    );

    return () => {
      document.body.removeEventListener("pointerup", dragEndHandler);
    };
  }, [isDisabled, dragEndHandler]);

  return (
    <DraggableContext.Provider
      value={{ isDisabled, longTouchClock, onDragMove }}
    >
      {children}
    </DraggableContext.Provider>
  );
}

export function useDraggableContext() {
  return useContext(DraggableContext);
}
