import styled from "@mui/system/styled";
import { useOptions } from "../options/OptionsProvider";
import { getMonsterColor } from "./colors";
import { display } from "@mui/system";

const Root = styled("div")(({ theme }) => ({
  "--from": 0,
  "--to": 0,
  "--spriteY": "0px",
  WebkitTapHighlightColor: "transparent",
  display: "block",
  width: 32,
  height: 32,
  zIndex: theme.zIndex.monster,
  overflow: "visible",
  backgroundImage: "url(./images/monster.png)",
  imageRendering: "pixelated",
  MozImageRendering: "crisp-edges",
  OImageRendering: "crisp-edges",
  WebkitImageRendering: "optimize-contrast",
  msInterpolationMode: "nearest-neighbor",
  transform: "scale(2)",
  transformOrigin: "center bottom",
  animationName: "spriteAnim",
  animationTimingFunction: "steps(2)",
  animationIterationCount: "infinite",
  animationDuration: "1s",
  animationFillMode: "forwards",

  "&.happy": {
    "--from": 0,
    "--to": "-64px",

    "&.archeologist::before": {
      content: '""',
      display: "block",
      position: "absolute",
      width: 24,
      height: 9,
      backgroundImage: "url(./images/items.png)",
      left: 4,
      transform: "translateY(0)",
      animationName: "jump",
      animationTimingFunction: "steps(2)",
      animationIterationCount: "infinite",
      animationDuration: "1s",
      animationFillMode: "forwards",

      "@keyframes jump": {
        "0%": {
          transform: "translateY(0)",
        },
        "100%": {
          transform: "translateY(-12px)",
        },
      },
    },
  },

  "&.hi": {
    "--from": "-64px",
    "--to": "-128px",
  },

  "&.speaking": {
    "--from": "-384px",
    "--to": "-448px",
    animationDuration: "500ms",
  },

  "&.walking": {
    "--from": "-128px",
    "--to": "-192px",
    animationName: "spriteAnim, moveRight",
    animationTimingFunction: "steps(2), linear",
    animationIterationCount: "infinite, 1",
    animationDuration: "600ms, 10s",

    "&.from-middle": {
      animationDelay: "0s, -5s",
    },

    "&.left": {
      animationName: "spriteAnim, moveLeft",
      transform: "scale(-2, 2)",
    },

    "&.old": {
      animationDuration: "600ms, 20s",

      "&.from-middle": {
        animationDelay: "0s, -10s",
      },
    },

    "&.flash": {
      animationDuration: "600ms, 5s",

      "&.from-middle": {
        animationDelay: "0s, -2.5s",
      },
    },
  },

  "&.shocked": {
    "--from": "-192px",
    "--to": "-192px",
    animationPlayState: "paused",
    "&.with-exclamation::before": {
      content: '"!"',
      position: "absolute",
      top: -20,
      left: 0,
      right: 0,
      textAlign: "center",
      color: "red",
      textShadow:
        "1px 1px #000, -1px 1px #000, -1px -1px #000, 1px -1px #000, 0px 1px #000, -1px 0px #000, 0px -1px #000, 1px 0 #000",
    },
  },

  "&.thinking": {
    "--from": "-224px",
    "--to": "-224px",
    animationPlayState: "paused",
  },

  "&.applausing": {
    "--from": "-256px",
    "--to": "-320px",
    animationDuration: "500ms",
  },

  "&.sweating": {
    "--from": "-320px",
    "--to": "-384px",
  },

  "&.nope": {
    "--from": "-448px",
    "--to": "-512px",
    animationDuration: "500ms",
  },

  "&.sad": {
    "--from": "-512px",
    "--to": "-576px",
    animationDuration: "1.5s",
  },

  "&.cool": {
    "--from": "-576px",
    "--to": "-640px",
    animationDuration: "500ms",
  },

  "&.hidden": {
    display: "none",
  },

  "@keyframes spriteAnim": {
    "0%": {
      backgroundPosition: "var(--from) var(--spriteY)",
    },
    "100%": {
      backgroundPosition: "var(--to) var(--spriteY)",
    },
  },

  "@keyframes moveRight": {
    "0%": {
      translate: "-70dvw",
    },
    "100%": {
      translate: "70dvw",
    },
  },

  "@keyframes moveLeft": {
    "0%": {
      translate: "70dvw",
    },
    "100%": {
      translate: "-70dvw",
    },
  },
}));

export default function Monster({
  state,
  isGoingToLeft = false,
  color = null,
  style = {},
  ...otherProps
}) {
  const { options } = useOptions();
  const colorIdToApply = color || options.monsterColor;
  const monsterColorId =
    process.env.IS_SITE && !["yellow", "light-green"].includes(colorIdToApply)
      ? "yellow"
      : colorIdToApply;
  const { spritePositionIndex } = getMonsterColor(monsterColorId);

  return (
    <Root
      key={state === "happy" && monsterColorId === "archeologist" ? 1 : 0}
      role={otherProps.onClick ? "button" : null}
      style={{
        ...style,
        "--spriteY": `-${spritePositionIndex * 32}px`,
      }}
      {...otherProps}
      className={` ${otherProps.className || ""} ${state || ""} ${
        isGoingToLeft ? "left" : ""
      } ${monsterColorId}`}
    />
  );
}
