import { flushSync } from "react-dom";

import Box from "@mui/system/Box";
import Stack from "@mui/system/Stack";

import { getAchievement } from "../../achievements";
import { useAchievements } from "../../achievements/AchievementsProvider";

import Enter from "../../common/icons/Enter";
import Erase from "../../common/icons/Erase";
import Shuffle from "../../common/icons/Shuffle";
import DownloadButton from "../../common/DownloadButton";
import FormControl from "../../common/form/FormControl";
import Link from "../../common/Link";
import Range from "../../common/form/Range";
import Select from "../../common/form/Select";
import SelectOption from "../../common/form/SelectOption";
import Button from "../../common/Button";
import Panel from "../../common/Panel";
import SectionContainer from "../../common/SectionContainer";

import { Key, KeyAction } from "../game/Key";
import { getLetters } from "../game/Alphabet";

import { colors as monsterColors } from "../../monster/colors";
import {
  hasUnlockedAchievement,
  hasUnlockedAllAchievements,
} from "../../save/achievements";
import { useOptions } from "../../options/OptionsProvider";

import { useCurrentSection } from "../SectionsProvider";

export default function Options() {
  const vars = useCurrentSection().currentSectionVars;
  const gameMode = vars.get("mode");

  const { options, dispatch } = useOptions();
  useAchievements();

  const getOptionUpdateHandler =
    (actionName, withTransition = false) =>
    (e) => {
      const { value } = e.target;

      const dispatchAction = () => {
        dispatch({
          type: actionName,
          payload: { value },
        });
      };

      if (!withTransition) {
        dispatchAction();
        return;
      }

      document.startViewTransition(() => {
        flushSync(dispatchAction);
      });
    };

  const keySubmit = (
    <KeyAction style={{ viewTransitionName: "submit" }}>
      <Enter variant="keyboard" />
    </KeyAction>
  );

  const keyDelete = (
    <KeyAction style={{ viewTransitionName: "delete" }}>
      <Erase variant="keyboard" />
    </KeyAction>
  );

  const rowLetters = getLetters(
    options.keyboardLayout,
    options.keyboardActionsKeys,
    null
  );

  return (
    <Box margin="auto" style={{ minWidth: "80vw" }}>
      <SectionContainer>
        <Stack component="form" gap={4} sx={{ width: "100%" }}>
          <Panel title="Options" sx={{ alignSelf: "center" }} />

          <Panel variant="subsection" title="Volume audio">
            <Stack flexDirection="column" gap={3} py={3}>
              {process.env.IS_SITE ? (
                <>
                  Dcouvrez les options sonores dans la version complte du jeu.
                  <br />
                  <div style={{ textAlign: "center" }}>
                    <DownloadButton scale={0.5} />
                  </div>
                </>
              ) : (
                <>
                  <FormControl label="Musique">
                    <Range
                      name="music"
                      min="0"
                      max="1"
                      step="0.1"
                      value={options.music}
                      onInput={getOptionUpdateHandler("setMusicVolume")}
                    />
                  </FormControl>

                  <FormControl label="Bruitages">
                    <Range
                      name="sounds"
                      min="0"
                      max="1"
                      step="0.1"
                      value={options.sounds}
                      onInput={getOptionUpdateHandler("setSoundsVolume")}
                    />
                  </FormControl>
                </>
              )}
            </Stack>
          </Panel>

          <Panel variant="subsection" title="Clavier">
            <Stack flexDirection="column" gap={3} py={3}>
              <FormControl label="Disposition des lettres">
                <Select
                  name="keyboardLayout"
                  value={options.keyboardLayout}
                  onChange={getOptionUpdateHandler("setKeyboardLayout", true)}
                >
                  <SelectOption value="AZERTY">AZERTY</SelectOption>
                  <SelectOption value="QWERTY">QWERTY</SelectOption>
                  <SelectOption value="ABCD">Ordre alphabtique</SelectOption>
                </Select>
              </FormControl>

              <FormControl label="Disposition des touches d'actions">
                <Select
                  name="keyboardActionsKeys"
                  value={options.keyboardActionsKeys}
                  onChange={getOptionUpdateHandler(
                    "setKeyboardActionsKeys",
                    true
                  )}
                >
                  <SelectOption value="delete-submit">
                    Effacement  gauche, validation  droite
                  </SelectOption>
                  <SelectOption value="submit-delete">
                    Validation  gauche, effacement  droite
                  </SelectOption>
                </Select>
              </FormControl>

              <FormControl label="Couleur des lettres">
                <Select
                  name="keyboardLettersColor"
                  value={options.keyboardLettersColor}
                  onChange={getOptionUpdateHandler("setKeyboardLettersColor")}
                >
                  <SelectOption value="grayscale(1) brightness(1.75) invert(1)">
                    Noir
                  </SelectOption>
                  <SelectOption value="grayscale(1) brightness(1.5)">
                    Gris
                  </SelectOption>
                  <SelectOption value="grayscale(1) brightness(2)">
                    Blanc
                  </SelectOption>
                </Select>
              </FormControl>

              <FormControl label="Couleur des touches d'actions">
                <Select
                  name="keyboardActionsColor"
                  value={options.keyboardActionsColor}
                  onChange={getOptionUpdateHandler("setKeyboardActionsColor")}
                >
                  <SelectOption value="grayscale(1) brightness(1.75) invert(1)">
                    Noir
                  </SelectOption>
                  <SelectOption value="grayscale(1) brightness(1.5)">
                    Gris
                  </SelectOption>
                  <SelectOption value="grayscale(1) brightness(2)">
                    Blanc
                  </SelectOption>
                </Select>

                <Stack
                  flexDirection="row"
                  justifyContent="center"
                  sx={{ pointerEvents: "none" }}
                >
                  {rowLetters[0].map((letter, index) =>
                    index < 6 ? (
                      <Key
                        key={letter}
                        style={{
                          viewTransitionName: letter,
                        }}
                      >
                        {letter}
                      </Key>
                    ) : null
                  )}
                </Stack>
                <Stack
                  flexDirection="row"
                  justifyContent="center"
                  sx={{ pointerEvents: "none" }}
                >
                  {options.keyboardActionsKeys === "submit-delete"
                    ? keySubmit
                    : keyDelete}
                  <KeyAction>
                    <Shuffle variant="keyboard" />
                  </KeyAction>
                  {options.keyboardActionsKeys !== "submit-delete"
                    ? keySubmit
                    : keyDelete}
                </Stack>
              </FormControl>
            </Stack>
          </Panel>

          <Panel variant="subsection" title="Arrire-plan">
            <Stack flexDirection="column" gap={3} py={3}>
              <FormControl label="Effet graphique">
                <Select
                  name="effectName"
                  value={options.backgroundEffectName}
                  onChange={getOptionUpdateHandler("setBackgroundEffect")}
                >
                  <SelectOption value="none">Aucun</SelectOption>
                  <SelectOption value="grayscale">Niveau de gris</SelectOption>
                  <SelectOption value="sepia">Sepia</SelectOption>
                </Select>
              </FormControl>

              <FormControl label="Luminosit">
                <Range
                  name="brightness"
                  min="0.1"
                  max="1"
                  step="0.1"
                  value={options.backgroundBrightness}
                  onInput={getOptionUpdateHandler("setBackgroundBrightness")}
                />
              </FormControl>
            </Stack>
          </Panel>

          <Panel variant="subsection" title="Autres">
            <Stack flexDirection="column" gap={3} py={3}>
              <FormControl label="Chronomtre">
                <Select
                  name="showTimer"
                  value={options.showTimer}
                  onChange={getOptionUpdateHandler("setShowTimer")}
                >
                  <SelectOption value="no">Sans</SelectOption>
                  <SelectOption value="yes">Avec</SelectOption>
                </Select>
              </FormControl>

              <FormControl label="Apparence du monstre">
                <Select
                  name="monsterColor"
                  value={options.monsterColor}
                  onChange={getOptionUpdateHandler("setMonsterColor")}
                >
                  {monsterColors.map(({ id, name, achievementId }, index) => {
                    if (process.env.IS_SITE && index > 1) {
                      return null;
                    }

                    const isUnlocked =
                      !achievementId || hasUnlockedAchievement(achievementId);
                    const isLocked =
                      (process.env.IS_SITE &&
                        achievementId !== "pock-monster" &&
                        achievementId !== null) ||
                      !isUnlocked;

                    return (
                      <SelectOption key={id} value={id} disabled={isLocked}>
                        {name}
                        {isLocked
                          ? ` (se dbloque via le succs "${getAchievement(
                              achievementId
                            ).name.replace(/&nbsp;/g, " ")}")`
                          : ""}
                      </SelectOption>
                    );
                  })}
                </Select>

                {hasUnlockedAllAchievements() ? (
                  process.env.IS_SITE && (
                    <div>
                      <i>
                        Dbloquez d'autres apparences pour le monstre dans la
                        version complte du jeu !
                      </i>
                      <br />
                      <br />
                      <div style={{ textAlign: "center" }}>
                        <DownloadButton scale={0.5} />
                      </div>
                    </div>
                  )
                ) : process.env.IS_SITE ? (
                  <div>
                    <i>
                      Dbloquez le succs "Ce monstre est trop mignon" pour
                      pouvoir jouer avec le monstre vert clair !
                    </i>
                  </div>
                ) : (
                  <div>
                    <i>
                      Dbloquez d'autres apparences pour le monstre en
                      russissant
                      <Link href="#achievements">des succs</Link> !
                    </i>
                  </div>
                )}
              </FormControl>
            </Stack>
          </Panel>

          {!process.env.IS_SITE && (
            <Panel sx={{ alignSelf: "center", textAlign: "center" }}>
              <p>
                Version de l'application :{" "}
                <b>v{window.App?.version ?? "0.0.0"}</b>
                <br />
                <Link
                  href={
                    gameMode
                      ? `#releaseNotes?mode=${gameMode}`
                      : "#releaseNotes"
                  }
                  sx={{ alignSelf: "center" }}
                >
                  Notes de version
                </Link>
              </p>
            </Panel>
          )}

          <Button
            variant="primary"
            size="small"
            component="a"
            href={gameMode ? `#game?mode=${gameMode}` : "#title"}
            sx={{ alignSelf: "center" }}
          >
            Retour
          </Button>
        </Stack>
      </SectionContainer>
    </Box>
  );
}
