import ConstraintButton from "../../common/ConstraintButton";
import Logo from "../../common/Logo";
import SectionContainer from "../../common/SectionContainer";
import DownloadButton from "../../common/DownloadButton";
import Cup from "../../common/icons/Cup";
import Gear from "../../common/icons/Gear";
import InterrogationMark from "../../common/icons/InterrogationMark";
import SlideUp from "../../common/SlideUp";
import { useAchievements } from "../../achievements/AchievementsProvider";
import { hasUnlockedAtLeastOneAchievement } from "../../save/achievements";

export default function Title() {
  useAchievements();

  return (
    <SectionContainer>
      <Logo />

      {process.env.IS_SITE && (
        <>
          <DownloadButton
            scale={0.75}
            sx={{ viewTransitionName: "main-download" }}
          />
          <br />
        </>
      )}

      {process.env.IS_SITE && (
        <>
          <SlideUp delay={50}>
            <ConstraintButton
              size="big"
              variant="tertiary"
              href="#explanations"
            >
              Comment jouer&nbsp;?
            </ConstraintButton>
          </SlideUp>
        </>
      )}

      <ConstraintButton
        size="big"
        href="#startGame"
        sx={{ viewTransitionName: "main-button" }}
      >
        Jouer
      </ConstraintButton>

      {process.env.IS_SITE && <br />}

      {hasUnlockedAtLeastOneAchievement() > 0 && (
        <SlideUp delay={100}>
          <ConstraintButton
            size="small"
            variant="secondary"
            href="#achievements"
            icon={<Cup />}
          >
            Succs
          </ConstraintButton>
        </SlideUp>
      )}

      <SlideUp delay={300}>
        <ConstraintButton variant="secondary" href="#options" icon={<Gear />}>
          Options
        </ConstraintButton>
      </SlideUp>

      {!process.env.IS_SITE && (
        <>
          <br />

          <SlideUp delay={600}>
            <ConstraintButton
              variant="tertiary"
              href="#explanations"
              icon={<InterrogationMark />}
            >
              Tutoriel
            </ConstraintButton>
          </SlideUp>
        </>
      )}

      <br />

      <SlideUp delay={600}>
        <ConstraintButton variant="tertiary" href="#policies" size="very-small">
          Politique de Confidentialit
        </ConstraintButton>
      </SlideUp>
    </SectionContainer>
  );
}
