import {
  useReducer,
  createContext,
  useContext,
  useEffect,
  useCallback,
  useRef,
} from "react";

import { useAchievements } from "../achievements/AchievementsProvider";

import {
  getGameModeData,
  getTodayDailyData,
  saveNewDailyGame,
  saveNewGame,
} from "../save/games";

import { getTodayTimestamp } from "../daily";
import {
  getRandomWord,
  getWordFromSeed,
  countCommonLetters,
  isValidWord,
} from "../words";

import gameReducer from "./gameReducer";

const GameContext = createContext({});

const DEFAULT_MODE = process.env.IS_SITE ? "daily" : "normal";

const gameModeToLetterCount = process.env.IS_SITE
  ? { daily: "?", tuto: 5 }
  : {
      easy: 4,
      normal: 5,
      hard: 6,
      daily: "?",
      tuto: 5,
    };

const initGameState = ({ gameMode }) => {
  const verifiedGameMode =
    gameMode in gameModeToLetterCount ? gameMode : DEFAULT_MODE;

  let wordToGuess = "";
  let suggestedWords = [];
  let lockedLetters = [];
  let highlightedLetters = {};
  let hasGivenUp = false;
  let currentTutorialStep = 1;
  let duration = 0;

  switch (verifiedGameMode) {
    case "daily": {
      const dailyData = getTodayDailyData();

      if (dailyData.wordToGuess) {
        wordToGuess = dailyData.wordToGuess;
        hasGivenUp = Boolean(dailyData.hasGivenUp);
        suggestedWords = Array.from(dailyData.suggestedWords ?? []);
        lockedLetters = Array.from(dailyData.lockedLetters ?? []);
        highlightedLetters = { ...dailyData.highlightedLetters };
        duration = dailyData.duration;
      } else {
        const timestamp = getTodayTimestamp();
        const dayIndexInWeek = new Date().getUTCDay();
        const dayIndexInMonth = new Date().getUTCDate();

        const seed =
          ((Math.floor(timestamp / 1000) + 1) * 2 +
            (Math.floor(timestamp / 10000) - 1) * 4 +
            dayIndexInMonth) *
            10 +
          dayIndexInWeek;

        wordToGuess = getWordFromSeed(seed);

        saveNewDailyGame(wordToGuess, timestamp);
      }
      break;
    }

    case "tuto": {
      const gameData = getGameModeData("tuto");
      wordToGuess = "PALME";
      hasGivenUp = false;

      if (gameData.wordToGuess) {
        suggestedWords = Array.from(gameData.suggestedWords ?? []);
        lockedLetters = Array.from(gameData.lockedLetters ?? []);
        highlightedLetters = { ...(gameData.highlightedLetters ?? {}) };
        currentTutorialStep = gameData.currentTutorialStep ?? 1;
        duration = gameData.duration;
      } else {
        saveNewGame("tuto", wordToGuess, { currentTutorialStep: 1 });
      }

      break;
    }

    default: {
      const gameData = getGameModeData(verifiedGameMode);

      if (gameData.wordToGuess) {
        wordToGuess = gameData.wordToGuess;
        hasGivenUp = Boolean(gameData.hasGivenUp);
        suggestedWords = Array.from(gameData.suggestedWords ?? []);
        lockedLetters = Array.from(gameData.lockedLetters ?? []);
        highlightedLetters = { ...gameData.highlightedLetters };
        duration = gameData.duration;
      } else {
        const letterCount =
          gameModeToLetterCount[verifiedGameMode] ||
          gameModeToLetterCount.normal;

        wordToGuess = getRandomWord(letterCount);

        saveNewGame(verifiedGameMode, wordToGuess);
      }
      break;
    }
  }

  return {
    gameMode: verifiedGameMode,
    wordToGuess,
    suggestedWords,
    lockedLetters,
    highlightedLetters,
    hasGivenUp,
    formError: "",
    currentTutorialStep,
    duration,
  };
};

export default function GameProvider({ children, mode, onGameEnd }) {
  const [gameState, dispatch] = useReducer(
    gameReducer,
    { gameMode: mode },
    initGameState
  );
  const timerRef = useRef(null);

  const { showAchievement } = useAchievements();

  const letterCount = gameState.wordToGuess.length;
  const suggestionCount = gameState.suggestedWords.length;
  const lastSuggestedWord = gameState.suggestedWords[suggestionCount - 1];
  const isTuto = mode === "tuto";

  const getCommonLetterCount = useCallback(
    (fromIndex) => {
      const wordToCheck = gameState.suggestedWords[suggestionCount + fromIndex];

      return isValidWord(wordToCheck)
        ? countCommonLetters(gameState.wordToGuess, wordToCheck)
        : 0;
    },
    [suggestionCount, gameState.suggestedWords, gameState.wordToGuess]
  );

  useEffect(() => {
    if (
      isTuto ||
      !lastSuggestedWord ||
      lastSuggestedWord === gameState.wordToGuess
    ) {
      return;
    }

    const isValid = isValidWord(lastSuggestedWord);

    const commonLettersCount = isValid
      ? countCommonLetters(gameState.wordToGuess, lastSuggestedWord)
      : "?";

    if (isValid) {
      if (commonLettersCount === letterCount) {
        showAchievement("anagram");
      } else if (commonLettersCount === 0) {
        showAchievement("no-common-letters");
      } else if (commonLettersCount === 3 && suggestionCount >= 3) {
        if (getCommonLetterCount(-2) === 2 && getCommonLetterCount(-3) === 1) {
          showAchievement("one-two-three");
        }
      }

      if (
        gameState.suggestedWords.filter((world) => world === lastSuggestedWord)
          .length > 1
      ) {
        showAchievement("repeat");
      }
    } else if (
      gameState.suggestedWords.filter((word) => !isValidWord(word)).length === 3
    ) {
      showAchievement("inventor");
    }
  }, [lastSuggestedWord, gameState.wordToGuess, isTuto, getCommonLetterCount]);

  useEffect(() => {
    timerRef.current = setInterval(() => {
      dispatch({ type: "incrementDuration" });
    }, 1000);

    return () => clearInterval(timerRef.current);
  }, []);

  if (gameState.hasGivenUp || lastSuggestedWord === gameState.wordToGuess) {
    onGameEnd();
    return null;
  }

  return (
    <GameContext.Provider value={{ gameState, dispatch }}>
      {children}
    </GameContext.Provider>
  );
}

export function useGameContext() {
  return useContext(GameContext);
}
