const DEFAULT_LAYOUT = "AZERTY";

export default async function getKeyboardLayout() {
  if (!navigator?.keyboard?.getLayoutMap) {
    return DEFAULT_LAYOUT;
  }

  try {
    const keyboardLayoutMap = await navigator.keyboard.getLayoutMap();

    const keyA = keyboardLayoutMap.get("KeyA");
    const keyQ = keyboardLayoutMap.get("KeyQ");

    if (keyA === "a" && keyQ === "q") {
      return "QWERTY";
    }

    if (keyA === "q" && keyQ === "a") {
      return "AZERTY";
    }

    return DEFAULT_LAYOUT;
  } catch (error) {
    return DEFAULT_LAYOUT;
  }
}
