let musicAudioContext, musicBuffer, musicGainNode, musicSrcNode;

async function getMusicAudioContext() {
  if (musicAudioContext) {
    return musicAudioContext;
  }

  musicAudioContext = new AudioContext();

  return new Promise((resolve) => {
    const onBufferDecoded = (buffer) => {
      musicBuffer = buffer;
      musicGainNode = musicAudioContext.createGain();
      musicGainNode.gain.value = 0;

      resolve(musicAudioContext);
    };

    musicAudioContext.decodeAudioData(musicBuffer, onBufferDecoded);
  });
}

export function setMusicVolume(volume) {
  if (musicGainNode) {
    musicGainNode.gain.value = volume;
  }
}

export async function playMusic() {
  if (process.env.IS_SITE) {
    return;
  }

  await getMusicAudioContext();

  if (musicSrcNode) {
    musicSrcNode.stop();
  }

  musicSrcNode = musicAudioContext.createBufferSource();
  musicSrcNode.buffer = musicBuffer;
  musicSrcNode.loop = true;
  musicSrcNode.connect(musicGainNode);
  musicGainNode.connect(musicAudioContext.destination);

  musicSrcNode.start();
}

export function stopMusic() {
  if (musicSrcNode) {
    musicSrcNode.stop();
  }
}

export function playSound(soundId, volume) {
  if (process.env.IS_SITE) {
    return;
  }

  const audio = document.getElementById(soundId);

  if (audio) {
    audio.volume = volume;
    audio.play();
  }
}

function getSupportedMusicFile() {
  const audio = document.createElement("audio");

  if (audio.canPlayType('audio/ogg; codecs="vorbis"')) {
    return "./sounds/music.ogg";
  }

  if (audio.canPlayType('audio/wav; codecs="1"')) {
    return "./sounds/music.wav";
  }

  return null;
}

export async function init() {
  const musicSrc = getSupportedMusicFile();

  if (!musicSrc) {
    return;
  }

  const response = await fetch(musicSrc);
  musicBuffer = await response.arrayBuffer();

  await getMusicAudioContext();
}
