import { Fragment } from "react";

import styled from "@mui/system/styled";

import { isValidWord, countCommonLetters } from "../../words";

import { getLetterClassname } from "../../game/gameStateSelectors";

const Root = styled("ol")(({ theme }) => ({
  width: "100%",
  padding: 0,
  display: "flex",
  flexWrap: "wrap",
  gap: theme.spacing(1),
  margin: theme.spacing(1, 0, 2, 0),
  justifyContent: "center",
  alignItems: "flexEnd",

  "@media (min-width:764px)": {
    gap: theme.spacing(2),
    margin: theme.spacing(2),
  },
}));

const Breakline = styled("li")(() => ({
  listStyle: "none",
  width: "100%",
  height: 0,
}));

export const Word = styled("li")(({ theme }) => ({
  ...theme.typography.word,
  display: "flex",
  color: theme.palette.text.main,
  backgroundColor: theme.palette.background.validWord,
  paddingLeft: theme.spacing(1),
  borderRadius: 4,
  boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
  position: "relative",
  overflow: "hidden",
}));

const NonValidWord = styled(Word)(({ theme }) => ({
  backgroundColor: theme.palette.background.nonValidWord,
}));

export const Letter = styled("span")(({ theme }) => ({
  margin: "3px 0.2px",
  padding: 1,
  border: "1px solid transparent",
  transform: "scale(0)",
  animation: "letter-appearence 333ms ease-in-out 1 forwards",

  "&.is-ok,&.is-locked": {
    backgroundColor: "rgb(194, 255, 194)",
    borderColor: "#000",
  },

  "&.is-not-ok": {
    backgroundColor: "rgb(255, 188, 188)",
    textDecoration: "underline",
  },

  "@keyframes letter-appearence": {
    "0%": {
      transform: "scale(1.5)",
      opacity: 0,
    },
    "50%": {
      transform: "scale(0.8)",
      opacity: 1,
    },
    "100%": {
      transform: "scale(1)",
      opacity: 1,
    },
  },
}));

const LetterOfInvalidWord = styled(Letter)(({ theme }) => ({
  color: theme.palette.text.counter,
  backgroundColor: theme.palette.background.counter,
  textDecoration: "none",
  fontStyle: "italic",
}));

export const Counter = styled("span")(({ theme }) => ({
  marginLeft: theme.spacing(1),
  top: 0,
  right: 0,
  background: theme.palette.background.counter,
  padding: theme.spacing(0, 1),
  color: theme.palette.text.counter,
  display: "flex",
  alignItems: "center",
  transform: "translateX(20px)",
  animation: "count-appearence 333ms ease-in 1 forwards",
  animationDelay: "150ms",

  "@keyframes count-appearence": {
    "0%": {
      opacity: 0,
    },
    "100%": {
      transform: "translateX(0)",
      opacity: 1,
    },
  },
}));

export default function WordList({ gameState }) {
  const totalWords = gameState.suggestedWords.length;

  return (
    <Root>
      {gameState.suggestedWords.map((word, index) => {
        const isValid = isValidWord(word);
        const WordComponent = isValid ? Word : NonValidWord;
        const LetterComponent = isValid ? Letter : LetterOfInvalidWord;

        return (
          <Fragment key={index}>
            {totalWords > 1 && index === totalWords - 1 && <Breakline />}
            <WordComponent>
              {word.split("").map((letter, letterIndex) => (
                <LetterComponent
                  className={
                    isValid
                      ? getLetterClassname(gameState, letter, letterIndex)
                      : ""
                  }
                  key={letterIndex}
                  style={{ animationDelay: `${letterIndex * 50}ms` }}
                >
                  {letter}
                </LetterComponent>
              ))}
              <Counter>
                {isValid
                  ? countCommonLetters(gameState.wordToGuess, word)
                  : "?"}
              </Counter>
            </WordComponent>
          </Fragment>
        );
      })}
    </Root>
  );
}
