import { useEffect } from "react";
import Box from "@mui/system/Box";
import Stack from "@mui/system/Stack";

import Button from "../../common/Button";
import Panel from "../../common/Panel";
import SectionContainer from "../../common/SectionContainer";
import SlideUp from "../../common/SlideUp";

import { useMainMonster } from "../../monster/MonsterProvider";
import { giveUpGame } from "../../save/games";

import { useCurrentSection } from "../SectionsProvider";

export default function EndGame() {
  const { setMonsterState, hideMonsterBubble } = useMainMonster();

  const vars = useCurrentSection().currentSectionVars;
  const gameMode = vars.get("mode");

  useEffect(() => {
    setMonsterState("sad");
    hideMonsterBubble();

    return () => {
      setMonsterState("walking");
    };
  }, []);

  return (
    <Box margin="auto">
      <SectionContainer>
        <Box mx={1}>
          <Panel title="Voulez-vous vraiment abandonner ?" />
        </Box>

        <Stack gap={5} mt={6} alignItems="center">
          <SlideUp delay={100}>
            <Button
              component="a"
              href={`#game?mode=${gameMode}`}
              variant="primary"
            >
              Non !
            </Button>
          </SlideUp>

          <SlideUp delay={300}>
            <Button
              component="a"
              onClick={() => {
                giveUpGame(gameMode);
              }}
              href={`#endGame?mode=${gameMode}`}
              variant="secondary"
              size="small"
            >
              Oui...
            </Button>
          </SlideUp>
        </Stack>
      </SectionContainer>
    </Box>
  );
}
