import { useState } from "react";
import { flushSync } from "react-dom";

import Stack from "@mui/system/Stack";
import styled from "@mui/system/styled";

import shuffleArray from "../../common/shuffleArray";
import DraggableProvider from "../../common/DraggableContext";
import Draggable from "../../common/Draggable";
import Enter from "../../common/icons/Enter";
import Erase from "../../common/icons/Erase";
import Shuffle from "../../common/icons/Shuffle";

import { useGameContext } from "../../game/GameProvider";

import {
  getLetterClassname,
  getLetterIndex,
  isLetterLocked,
  hasLockedLetters,
} from "../../game/gameStateSelectors";

import { useOptions } from "../../options/OptionsProvider";
import { useSounds } from "../../sounds/SoundsProvider";

import { Key, KeyAction } from "./Key";

const CheckboxContainer = styled("label")({
  overflow: "hidden",
  cursor: "pointer",
  viewTransitionName: "shuffle-checkbox-container",
});

const CheckboxLabel = styled("span")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  gap: theme.spacing(2),
  fontSize: "1rem",
  borderRadius: 8,
  padding: theme.spacing(2),
  color: theme.palette.text.counter,
  background: theme.palette.background.counter,

  "&:before": {
    display: "inline-block",
    content: "''",
    width: 24,
    height: 24,
    backgroundColor: theme.palette.background.input,
    color: theme.palette.text.main,
    borderRadius: 4,
    flexShrink: 0,
    textAlign: "center",
  },
}));

const HiddenCheckBox = styled("input")({
  opacity: 0,
  position: "absolute",
  left: -999,
  height: 0,
  width: 0,
});

const DraggedKey = styled(Key)(({ theme }) => ({
  position: "fixed",
  zIndex: theme.zIndex.draggedElement,
  pointerEvents: "none",
}));

export default function ShuffleLetters({
  onLetterAdd,
  onErase,
  onActiveChange,
  letters = [],
}) {
  const { gameState } = useGameContext();
  const [selectedLetter, setSelectedLetter] = useState(null);
  const [draggedKeyStyle, setDraggedKeyStyle] = useState({});

  const { playSound } = useSounds();
  const { options } = useOptions();

  const shuffleWithLocked = (lettersToShuffle) => {
    const notLockedLetters = shuffleArray(
      lettersToShuffle.filter((x) => !gameState.lockedLetters.includes(x))
    );

    const lettersToReturn = new Array(letters.length).fill(null);

    gameState.lockedLetters.forEach((lockedLetter) => {
      lettersToReturn[getLetterIndex(gameState, lockedLetter)] = lockedLetter;
    });

    return lettersToReturn.map((letter) =>
      letter ? letter : notLockedLetters.pop()
    );
  };

  const [shuffledLetters, setShuffleLetters] = useState(
    shuffleWithLocked(letters)
  );

  const isActive = letters.length > 0;

  const shuffle = () => {
    playSound("click");
    document.startViewTransition(() => {
      flushSync(() => {
        setShuffleLetters((prev) => {
          let nextLetterOrder = [];

          let tries = 0;

          do {
            nextLetterOrder = shuffleWithLocked(prev);
            tries++;
          } while (prev.toString() === nextLetterOrder.toString() && tries < 2);

          return nextLetterOrder;
        });
      });
    });
  };

  const swapLetters = (letter1, letter2) => {
    setShuffleLetters((prevLetters) => {
      const index1 = prevLetters.indexOf(letter1);
      const index2 = prevLetters.indexOf(letter2);

      if (index1 === -1 || index2 === -1) {
        console.error(
          "Une ou les deux lettres n'existent pas dans le tableau."
        );
        return;
      }

      [prevLetters[index1], prevLetters[index2]] = [
        prevLetters[index2],
        prevLetters[index1],
      ];

      return prevLetters;
    });
  };

  const keySubmit = (
    <KeyAction
      type="submit"
      aria-label="Soumettre"
      style={{ viewTransitionName: "enter" }}
    >
      <Enter variant="keyboard" />
    </KeyAction>
  );

  const keyDelete = (
    <KeyAction
      type="button"
      onClick={onErase}
      aria-label="Effacer"
      style={{ viewTransitionName: "erase" }}
    >
      <Erase variant="keyboard" />
    </KeyAction>
  );

  return (
    <>
      {isActive && (
        <>
          {selectedLetter && (
            <DraggedKey
              className="is-ok"
              type="button"
              disabled
              style={draggedKeyStyle}
            >
              {selectedLetter}
            </DraggedKey>
          )}
          <DraggableProvider
            onDragMove={({ left, top, deltaX, deltaY }) => {
              setDraggedKeyStyle({
                left: `${left}px`,
                top: `${top - 32}px`,
                transform: `translate(${deltaX}px,${deltaY}px)`,
              });
            }}
            onDragEnd={() => {
              setSelectedLetter(null);
              setDraggedKeyStyle({});
            }}
          >
            <Stack flexDirection="row" justifyContent="center" flexWrap="wrap">
              {shuffledLetters.filter(Boolean).map((letter) => (
                <Draggable
                  key={letter}
                  onClick={() => onLetterAdd(letter)}
                  onDragStart={() => setSelectedLetter(letter)}
                  isDisabled={isLetterLocked(gameState, letter)}
                >
                  <Key
                    type="button"
                    onPointerMove={() =>
                      selectedLetter &&
                      selectedLetter !== letter &&
                      !isLetterLocked(gameState, letter) &&
                      swapLetters(letter, selectedLetter)
                    }
                    style={{
                      opacity: letter === selectedLetter ? 0.5 : 1,
                      viewTransitionName: letter,
                    }}
                    className={getLetterClassname(gameState, letter)}
                  >
                    {letter}
                  </Key>
                </Draggable>
              ))}
            </Stack>
          </DraggableProvider>

          <Stack
            flexDirection="row"
            justifyContent="center"
            sx={{ pointerEvents: selectedLetter ? "none" : "all" }}
          >
            {options.keyboardActionsKeys === "submit-delete"
              ? keySubmit
              : keyDelete}
            <KeyAction
              type="button"
              onClick={shuffle}
              aria-label="Mlanger"
              style={{ viewTransitionName: "shuffle" }}
            >
              <Shuffle variant="keyboard" />
            </KeyAction>
            {options.keyboardActionsKeys !== "submit-delete"
              ? keySubmit
              : keyDelete}
          </Stack>
        </>
      )}

      {!hasLockedLetters(gameState) && (
        <CheckboxContainer>
          <HiddenCheckBox
            type="checkbox"
            checked={isActive}
            onChange={(e) => onActiveChange(e.target.checked)}
          />
          <CheckboxLabel
            sx={isActive ? { "&:before": { content: '""' } } : null}
          >
            Mettre en avant les lettre vertes
          </CheckboxLabel>
        </CheckboxContainer>
      )}
    </>
  );
}
