import { useDraggableContext } from "./DraggableContext";

export default function Draggable({
  children,
  onClick,
  onDragStart,
  isDisabled: isDragOnElementDisabled = false,
}) {
  const {
    isDisabled: isDragGloballyDisabled,
    longTouchClock,
    onDragMove,
  } = useDraggableContext();

  const isDisabled = isDragGloballyDisabled || isDragOnElementDisabled;

  const onPointerDown = (e) => {
    e.target.releasePointerCapture(e.pointerId);

    if (!isDisabled) {
      const elementBoundingBox = e.target.getBoundingClientRect();
      const initialPointerPosition = { x: e.clientX, y: e.clientY };

      onDragMove({
        left: elementBoundingBox.x,
        top: elementBoundingBox.y,
        deltaX: 0,
        deltaY: 0,
      });

      function dragMoveHandler(dragEvent) {
        const deltaX = dragEvent.clientX - initialPointerPosition.x;
        const deltaY = dragEvent.clientY - initialPointerPosition.y;

        onDragMove({
          left: elementBoundingBox.x,
          top: elementBoundingBox.y,
          deltaX,
          deltaY,
        });
      }

      document.body.addEventListener("pointermove", dragMoveHandler);
      document.body.addEventListener(
        "pointerup",
        () => {
          document.body.removeEventListener("pointermove", dragMoveHandler);
        },
        { once: true }
      );

      longTouchClock.current = window.setTimeout(() => {
        longTouchClock.current = null;
        e.target.removeEventListener("pointerup", addLetter);
        onDragStart();

        if (navigator.vibrate) {
          navigator.vibrate(100);
        }
      }, 400);
    }

    e.target.addEventListener("pointerup", addLetter, { once: true });

    function addLetter() {
      if (longTouchClock.current) {
        window.clearTimeout(longTouchClock.current);
      }

      longTouchClock.current = null;
      onClick();
    }
  };

  return (
    <span
      onPointerDown={onPointerDown}
      onContextMenu={(e) => {
        e.preventDefault();
      }}
    >
      {children}
    </span>
  );
}
