import styled from "@mui/system/styled";

import { useOptions } from "../../options/OptionsProvider";
import { fontStyle, fontWeight } from "@mui/system";

export const BaseKey = styled("button")(({ theme }) => ({
  width: 32,
  height: 64,
  border: 0,
  padding: 0,
  cursor: "pointer",
  backgroundColor: "transparent",
  fontFamily: "monospace",
  borderImage: "url(./images/letter.png)",
  borderImageRepeat: "round",
  borderImageWidth: "auto",
  borderImageSlice: "0 fill",
  color: theme.palette.border.button,
  fontWeight: "bold",
  userSelect: "none",
  textAlign: "center",
  boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
  display: "inline-flex",
  justifyContent: "center",
  alignItems: "center",
  verticalAlign: "bottom",
  margin: theme.spacing(1, 0.25),
  fontSize: "1rem",
  touchAction: "none",
  position: "relative",
  "-webkit-touch-callout": "none !important",
  "-webkit-user-select": "none !important",

  "&.is-ok": {
    filter: "hue-rotate(310deg) brightness(1.2) !important",
    backgroundColor: "#129cbc",
    "&:before": {
      content: '""',
      display: "block",
      position: "absolute",
      inset: 0,
      pointerEvents: "none",
      boxShadow: "inset 3px 3px #000, inset -3px -3px #000",
    },
  },

  "&.is-not-ok": {
    filter: "hue-rotate(170deg) brightness(1.2) !important",
    textDecoration: "underline",
    backgroundColor: "#00b7e2",
  },

  "&.is-locked": {
    filter: "brightness(1.5) !important",
    fontWeight: "bolder",
    backgroundColor: "#00b7e2",
    fontStyle: "italic",
    "&:before": {
      content: '""',
      display: "block",
      position: "absolute",
      inset: 0,
      pointerEvents: "none",
      boxShadow: "inset 3px 3px #000, inset -3px -3px #000",
    },
  },

  "&:hover": {
    boxShadow: "0 2px 4px rgba(0, 0, 0, 0.3)",
    transform: "translateY(-2px)",
  },

  "&:active": {
    boxShadow: "0 6px 8px rgba(0, 0, 0, 0.45)",
    transform: "translateY(1px)",
  },

  "@media (min-width:470px)": {
    width: 40,
  },

  "@media (min-width:540px)": {
    width: 48,
    margin: theme.spacing(1, 0.5),
  },

  "@media (min-width:610px)": {
    width: 56,
    fontSize: "1.5rem",
  },

  "@media (min-width:680px)": {
    width: 64,
  },

  "@media (min-width:750px)": {
    width: 70,
    height: 70,
    fontSize: "2rem",
  },
}));

export function Key({ style = {}, ...otherProps }) {
  const { options } = useOptions();

  return (
    <BaseKey
      {...otherProps}
      style={{ ...style, filter: options.keyboardLettersColor }}
    />
  );
}

const BaseKeyAction = styled(BaseKey)(() => ({
  width: 64,

  "@media (min-width:470px)": {
    width: 80,
  },

  "@media (min-width:540px)": {
    width: 96,
  },

  "@media (min-width:610px)": {
    width: 112,
  },

  "@media (min-width:680px)": {
    width: 128,
  },

  "@media (min-width:750px)": {
    width: 140,
  },
}));

export function KeyAction({ style = {}, ...otherProps }) {
  const { options } = useOptions();

  return (
    <BaseKeyAction
      {...otherProps}
      style={{ ...style, filter: options.keyboardActionsColor }}
    />
  );
}
