import styled from "@mui/system/styled";

import Link from "../../common/Link";
import DownloadButton from "../../common/DownloadButton";
import Panel from "../../common/Panel";

const Root = styled(Panel)(({ theme }) => ({
  padding: theme.spacing(6),
  paddingBottom: 0,
  margin: theme.spacing(3),
  textAlign: "center",
}));

export default function DownloadAppPanel({ onLinkClick }) {
  return (
    <Root title="Tutoriel termin !">
      <p>
        Pourquoi ne pas essayer{" "}
        <Link onClick={onLinkClick} href="#startGame?mode=daily">
          le mot du jour
        </Link>
        &nbsp;? Chaque jour, un nouveau mot est  dcouvrir&nbsp;: affrontez vos
        proches et soyez le plus efficace dans la recherche des
        mots-mystres&nbsp;!
        <br />
        Profitez galement de la version complte du jeu, tlchargeable via le
        bouton ci-dessous&nbsp;!
        <br />
        <br />
        <DownloadButton scale={0.75} />
      </p>
    </Root>
  );
}
