import styled from "@mui/system/styled";

const Root = styled("span")(() => ({
  filter:
    "drop-shadow(-1px 0px 0px white) drop-shadow(1px 0px 0px white) drop-shadow(0px -1px 0px white) drop-shadow(0px 1px 0px white)",
  width: "100%",
  display: "flex",
}));

const SvgIcon = styled("svg")(() => ({
  width: "100%",
  height: "100%",
}));

export default function Icon({ children, ...otherProps }) {
  return (
    <Root {...otherProps}>
      <SvgIcon focusable="false" aria-hidden="true" viewBox="0 0 24 24">
        {children}
      </SvgIcon>
    </Root>
  );
}
