import { useEffect } from "react";
import styled from "@mui/system/styled";

import { achievements } from "../../achievements";
import Achievement from "../../achievements/Achievement";
import Link from "../../common/Link";
import SectionContainer from "../../common/SectionContainer";
import { useAchievements } from "../../achievements/AchievementsProvider";

import Button from "../../common/Button";
import DownloadButton from "../../common/DownloadButton";
import Panel from "../../common/Panel";
import SlideUp from "../../common/SlideUp";
import { useOptions } from "../../options/OptionsProvider";

import { getMonsterColorByAchievementId } from "../../monster/colors";
import { useMainMonster } from "../../monster/MonsterProvider";

import { hasUnlockedAllAchievements } from "../../save/achievements";

const TitlePanel = styled(Panel)(({ theme }) => ({
  marginBottom: theme.spacing(1),
  padding: theme.spacing(3),
  position: "relative",
  zIndex: theme.zIndex.neutral,
  boxSizing: "border-box",
  textAlign: "center",
  display: "block",
}));

const List = styled("ul")(({ theme }) => ({
  listStyle: "none",
  padding: theme.spacing(5, 0),
  margin: 0,
  display: "flex",
  flexDirection: "row",
  flexWrap: "wrap",
  justifyContent: "center",
  gap: theme.spacing(3),
}));

export default function Achievements() {
  useAchievements();

  const { setMonsterState, showMonsterBubble, hideMonsterBubble } =
    useMainMonster();

  const shouldMakeMonsterApplausing = hasUnlockedAllAchievements();

  useEffect(() => {
    if (shouldMakeMonsterApplausing) {
      setMonsterState(process.env.IS_SITE ? "thinking" : "applausing");
      showMonsterBubble(
        process.env.IS_SITE ? (
          <>
            Ce succs n'tait pas trs difficile  trouver, je me demande si
            vous arriverez  dbloquer ceux du jeu complet...
          </>
        ) : (
          <>
            Vous avez dbloqu <b>tous</b> les succs ! Bravo  vous, je suis
            impressionn !
          </>
        )
      );

      return () => {
        setMonsterState("walking");
        hideMonsterBubble();
      };
    }
  }, [shouldMakeMonsterApplausing]);

  const { dispatch } = useOptions();

  const getOnClick = (achievementId) => () => {
    dispatch({
      type: "setMonsterColor",
      payload: { value: getMonsterColorByAchievementId(achievementId).id },
    });
  };

  return (
    <SectionContainer>
      <TitlePanel title="Succs">
        {process.env.IS_SITE ? (
          <>
            Vous avez trouv le succs cach dans ce site, flicitations !<br />
            <i>
              Chaque succs dbloqu vous permet d'appliquer une nouvelle
              apparence au petit monstre depuis{" "}
              <Link href="#options">les options</Link>.
            </i>
            <br />
            <br />
            Le jeu complet propose une <strong>vingtaine</strong> de succs 
            dbloquer.
            <br />
            <br />
            <div style={{ textAlign: "center" }}>
              <DownloadButton scale={0.5} />
            </div>
          </>
        ) : (
          <>
            Retrouvez ici les succs que vous avez dbloqus.
            <br />
            <i>
              Chaque succs dbloqu vous permet d'appliquer une nouvelle
              apparence au petit monstre depuis{" "}
              <Link href="#options">les options</Link>.
            </i>
          </>
        )}
      </TitlePanel>

      <List>
        {achievements
          .filter(({ id }) => !process.env.IS_SITE || id === "pock-monster")
          .map(({ id }, index) => (
            <li key={id}>
              <SlideUp delay={index * 50}>
                <Achievement id={id} onClick={getOnClick(id)} />
              </SlideUp>
            </li>
          ))}
      </List>

      <Button component="a" variant="primary" size="small" href="#title">
        Retour
      </Button>
    </SectionContainer>
  );
}
