import styled from "@mui/system/styled";

export default styled("input")(({ theme }) => ({
  display: "inline-block",
  fontSize: "2rem",
  textTransform: "uppercase",
  textAlign: "center",
  border: 0,
  borderRadius: "1rem",
  padding: theme.spacing(2),
  background: theme.palette.background.panel,
  color: theme.palette.text.panel,
  boxShadow: `1px 1px ${theme.palette.shadow.input}`,
  cursor: "pointer",
  boxSizing: "content-box",
  width: "70%",
  maxWidth: "10rem",

  "&:hover": {
    filter: "brightness(1.1)",
  },
  "&:focus,&:active": {
    filter: "brightness(1.2)",
    outline: 0,
  },
}));
