import { Fragment, useEffect, useState } from "react";
import Box from "@mui/system/Box";
import styled from "@mui/system/styled";

import Button from "../../common/Button";
import Panel from "../../common/Panel";
import ShowHide from "../../common/ShowHide";
import SectionContainer from "../../common/SectionContainer";
import { formatDuration } from "../../common/time";

import { useAchievements } from "../../achievements/AchievementsProvider";
import { useMainMonster } from "../../monster/MonsterProvider";
import { getGameModeData, deleteGameData } from "../../save/games";
import { getValidTryCount } from "../../game/gameStateSelectors";

import { useCurrentSection } from "../SectionsProvider";

import DownloadAppPanel from "./DownloadAppPanel";
import NextDailyPanel from "./NextDailyPanel";
import ShareButton from "./ShareButton";
import { countCommonLetters, isValidWord } from "../../words";
import WordList from "../game/WordList";

const TriesPanel = styled(Panel)(({ theme }) => ({
  padding: theme.spacing(3),
  textAlign: "center",
  maxWidth: 550,
}));

export default function EndGame() {
  const { showAchievement } = useAchievements();
  const { setMonsterState, showMonsterBubble, hideMonsterBubble } =
    useMainMonster();
  const [canReplayDaily, setCanReplayDaily] = useState(false);

  const vars = useCurrentSection().currentSectionVars;
  let gameMode = vars.get("mode");

  if (process.env.IS_SITE && gameMode !== "tuto" && gameMode !== "daily") {
    gameMode = "daily";
  }

  const isDaily = gameMode === "daily";
  const isTuto = gameMode === "tuto";

  const gameData = getGameModeData(gameMode);

  const tryCount = getValidTryCount(gameData);
  const hasWon =
    !gameData.hasGivenUp &&
    gameData.suggestedWords?.includes(gameData.wordToGuess);

  const durationFormated = formatDuration(gameData.duration, true);

  useEffect(() => {
    let monsterMessage = [];

    const wordLink = (
      <a
        id="endGameWord"
        target="_blank"
        href={`https://dictionnaire.lerobert.com/definition/${gameData.wordToGuess}`}
      >
        {gameData.wordToGuess}
      </a>
    );

    let newMonsterState = "speaking";

    if (hasWon) {
      showAchievement("first-word");

      if (isDaily) {
        showAchievement("daily-word");
      } else if (!isTuto) {
        const achievementDelay = 60 * 2; // Two minutes in seconds
        const gameDurationInSeconds = gameData.duration;

        if (gameDurationInSeconds <= achievementDelay) {
          showAchievement("quick-win");
        }
      }

      if (gameData.wordToGuess.length === 7) {
        showAchievement("seven-letters-word");
      }

      monsterMessage.push(
        <>
          Bravo, vous avez trouv le {isDaily ? "mot du jour" : "mot-mystre"}
          &nbsp;!
        </>
      );

      monsterMessage.push(
        <>
          Il s'agissait bien de {wordLink}.<br />
          Cela vous a pris <b>{durationFormated}</b>.
        </>
      );

      switch (true) {
        case tryCount === 1: {
          monsterMessage.push(
            <>
              Vous l'avez trouv <strong>DU PREMIER</strong> coup&nbsp;! C'est
              vraiment dingue, comment vous avez fait...&nbsp;?!
            </>
          );
          newMonsterState = "shocked";

          showAchievement("first-try");

          if (!isDaily && !isTuto) {
            showAchievement("tries-20");
            showAchievement("tries-10");
            showAchievement("tries-5");
          }
          break;
        }

        case tryCount === 2:
        case tryCount === 3: {
          monsterMessage.push(
            <>
              Vous l'avez trouv au{" "}
              <strong>{tryCount === 2 ? "DEUXIME" : "TROISIME"}</strong>{" "}
              coup&nbsp;! C'est trs impressionnant&nbsp;!
            </>
          );
          newMonsterState = "shocked";

          if (!isDaily && !isTuto) {
            showAchievement("tries-20");
            showAchievement("tries-10");
            showAchievement("tries-5");
          }
          break;
        }

        case tryCount < 6: {
          monsterMessage.push(
            <>
              Vous l'avez trouv en <strong>seulement {tryCount}</strong>{" "}
              coups&nbsp;! C'est super classe&nbsp;!
            </>
          );
          newMonsterState = "cool";

          if (!isDaily && !isTuto) {
            showAchievement("tries-20");
            showAchievement("tries-10");
            showAchievement("tries-5");
          }
          break;
        }

        case tryCount < 11: {
          monsterMessage.push(
            <>
              Vous l'avez trouv en <strong>{tryCount}</strong> coups&nbsp;!
              C'est bien jou&nbsp;!
            </>
          );
          newMonsterState = "happy";

          if (!isDaily && !isTuto) {
            showAchievement("tries-20");
            showAchievement("tries-10");
          }
          break;
        }

        case tryCount >= 11 && tryCount <= 13: {
          monsterMessage.push(
            <>
              Vous l'avez trouv en <strong>{tryCount}</strong> coups : bien
              jou, c'est dans la moyenne&nbsp;!
            </>
          );
          newMonsterState = "applausing";

          if (!isDaily && !isTuto) {
            showAchievement("tries-20");
          }
          break;
        }

        case tryCount < 21: {
          monsterMessage.push(
            <>
              Vous l'avez trouv en <strong>{tryCount}</strong> coups&nbsp;!
            </>
          );
          newMonsterState = "applausing";

          if (!isDaily && !isTuto) {
            showAchievement("tries-20");
          }
          break;
        }

        default: {
          monsterMessage.push(
            <>
              Vous l'avez trouv en <strong>{tryCount}</strong> coups : vous
              pouvez sans doute faire mieux&nbsp;!
            </>
          );
          newMonsterState = "sweating";

          if (tryCount >= 25) {
            showAchievement("tries-30");
          }
          break;
        }
      }
    } else {
      newMonsterState = "sweating";

      monsterMessage.push(
        <>
          Vous n'avez pas trouv le {isDaily ? "mot du jour" : "mot-mystre"}...
        </>
      );

      monsterMessage.push(<>Il s'agissait de {wordLink}.</>);

      monsterMessage.push(
        <>
          Vous avez abandonn aprs <b>{durationFormated}</b>, pour un total de{" "}
          <b>{tryCount}</b> tentative
          {tryCount > 1 ? "s" : ""} : vous y arriverez la prochaine fois&nbsp;!
        </>
      );

      showAchievement("give-up");
    }

    setMonsterState(newMonsterState);
    showMonsterBubble(
      monsterMessage.map((message, index) => (
        <Fragment key={index}>
          {message}
          {index < monsterMessage.length - 1 ? <br /> : null}
        </Fragment>
      ))
    );

    return () => {
      setMonsterState("walking");
      hideMonsterBubble();
    };
  }, [isDaily, hasWon, tryCount, gameData.wordToGuess, durationFormated]);

  const deleteGameSave = () => deleteGameData(gameMode);
  const canReplay = !process.env.IS_SITE && (!isDaily || canReplayDaily);
  const canShare = isDaily && navigator.share;

  return (
    <Box margin="auto">
      <SectionContainer>
        <Panel title={hasWon ? "Flicitations !" : "Dommage..."}></Panel>

        {canReplay && (
          <Button
            component="a"
            href={`#startGame?mode=${isTuto ? "normal" : gameMode}`}
            variant="primary"
            onClick={deleteGameSave}
          >
            Rejouer
          </Button>
        )}

        <Button
          component="a"
          href={`#title`}
          variant={canReplay ? "secondary" : "primary"}
          size={canReplay ? "small" : "big"}
          onClick={deleteGameSave}
        >
          Accueil
        </Button>

        {!isTuto && (
          <TriesPanel>
            <ShowHide label="Voir vos tentatives">
              <WordList gameState={gameData} />
            </ShowHide>

            {isDaily && (
              <>
                {canShare && (
                  <Box sx={{ marginTop: 2 }}>
                    <ShareButton
                      hasWon={hasWon}
                      tryCount={tryCount}
                      duration={durationFormated}
                      wordCountHistory={gameData.suggestedWords?.map(
                        (suggestedWord) =>
                          isValidWord(suggestedWord)
                            ? countCommonLetters(
                                suggestedWord,
                                gameData.wordToGuess
                              )
                            : "?"
                      )}
                    />
                  </Box>
                )}
              </>
            )}
          </TriesPanel>
        )}

        {!canReplay &&
          (isDaily ? (
            <NextDailyPanel onEnd={() => setCanReplayDaily(true)} />
          ) : (
            <DownloadAppPanel onLinkClick={deleteGameSave} />
          ))}
      </SectionContainer>
    </Box>
  );
}
