import {
  useState,
  createContext,
  useEffect,
  useCallback,
  useContext,
} from "react";
import { flushSync } from "react-dom";

import styled from "@mui/system/styled";

import { useGameContext } from "../game/GameProvider";
import { useOptions } from "../options/OptionsProvider";

import { useMainMonster } from "../monster/MonsterProvider";

const TutorialContext = createContext({
  disabledInteractions: {
    suggestWord: false,
    dragKeys: false,
  },
});

function scrollToBottom() {
  document.getElementById("app").scrollTo({
    top: document.body.scrollHeight,
    behavior: "smooth",
  });
}

export default function TutoProvider({ children }) {
  const [expectedWord, setExpectedWord] = useState("");
  const [disabledInteractions, setDisabledInteractions] = useState({
    suggestWord: false,
    dragKeys: false,
  });
  const [onDocumentClick, setOnDocumentClick] = useState(null);
  const [lastScrolledStep, setLastScrolledStep] = useState(0);

  const { options } = useOptions();

  const { gameState, dispatch } = useGameContext();
  const tutoStep = gameState.currentTutorialStep;

  const { setMonsterState, showMonsterBubble, hideMonsterBubble } =
    useMainMonster();

  const goToNextStep = () => dispatch({ type: "goToNextTutorialStep" });

  const lastSuggestedWord =
    gameState.suggestedWords[gameState.suggestedWords.length - 1];

  const highlightedLetters = gameState.highlightedLetters;

  const renderStep = useCallback(
    (stepCallback, scrollDelay = 1000) => {
      window.requestAnimationFrame(() => {
        flushSync(stepCallback);

        if (tutoStep !== lastScrolledStep) {
          window.setTimeout(scrollToBottom, scrollDelay);

          setLastScrolledStep(tutoStep);
        }
      });
    },
    [tutoStep, lastScrolledStep]
  );

  useEffect(() => {
    if (onDocumentClick) {
      document.addEventListener("click", onDocumentClick, { once: true });
    }

    return () => document.removeEventListener("click", onDocumentClick);
  }, [onDocumentClick]);

  useEffect(() => {
    switch (tutoStep) {
      case 1: {
        renderStep(() => {
          setMonsterState("speaking");
          showMonsterBubble(
            <>
              Il faut commencer par proposer un mot&nbsp;!
              <br />
              Le mot-mystre n'a que des lettres diffrentes, mais vous pouvez
              proposer des mots avec des lettres en double.
              <br />
              Dans une partie normale, le premier mot que vous proposez peut
              tre celui que vous voulez. Mais pour ce tuto, utilisez le clavier
              pour essayer le mot <b>MILLE</b>.
              <br />
              N'oubliez pas d'appuyer sur la touche "Entre" du clavier (en bas
              {" "}
              {options.keyboardActionsKeys === "submit-delete"
                ? "gauche"
                : "droite"}
              ) pour valider le mot&nbsp;!
            </>
          );

          setDisabledInteractions({
            suggestWord: false,
            dragKeys: true,
          });

          if (lastSuggestedWord === "MILLE") {
            goToNextStep();
            setExpectedWord("");
          } else {
            setExpectedWord("MILLE");
          }
        }, 0);

        break;
      }

      case 2: {
        renderStep(() => {
          setMonsterState("happy");
          showMonsterBubble(
            <>
              Le mot <b>MILLE</b> a donc <b>3</b> lettres en commun avec le
              mot-mystre : ce n'est pas mal du tout !
              <br />
              Essayons un mot proche de <b>MILLE</b>&nbsp;: entrez le mot{" "}
              <b>BILLE</b>.
            </>
          );

          setDisabledInteractions({
            suggestWord: false,
            dragKeys: true,
          });

          if (lastSuggestedWord === "BILLE") {
            goToNextStep();
            setExpectedWord("");
          } else {
            setExpectedWord("BILLE");
          }
        });

        break;
      }

      case 3: {
        renderStep(() => {
          setMonsterState("thinking");
          showMonsterBubble(
            <>
              <b>BILLE</b> a <b>2</b> lettres en commun avec le mot-mystre
              alors que <b>MILLE</b> en a <b>3</b>&nbsp;: nous avons remplac le{" "}
              <kbd>M</kbd> par un <kbd>B</kbd> et nous avons perdu un point. On
              peut donc en dduire que le mot-mystre n'a <b>pas</b> de{" "}
              <kbd>B</kbd> et possde un <kbd>M</kbd>&nbsp;!
            </>,
            true
          );

          setOnDocumentClick(() => goToNextStep);

          setDisabledInteractions({
            suggestWord: true,
            dragKeys: true,
          });
        });

        break;
      }

      case 4: {
        renderStep(() => {
          setMonsterState("speaking");
          showMonsterBubble(
            <>
              Pour vous aider  tenir le compte des lettres que vous pensez tre
              prsentes ou non dans le mot-mystre, vous pouvez les marquer en
              vert ou rouge.
              <br />
              Appuyez longuement sur le <kbd>B</kbd> et glissez-le {" "}
              <b>gauche</b> de l'cran pour le marquer en{" "}
              <span style={{ color: "#bf5658" }}>rouge</span>.
              <br />
              Faites pareil pour le <kbd>M</kbd>, mais glissez-le {" "}
              <b>droite</b> pour le marquer en{" "}
              <span style={{ color: "#22914c" }}>vert</span>.
            </>
          );

          setDisabledInteractions({
            suggestWord: true,
            dragKeys: false,
          });

          if (highlightedLetters.B === 0 && highlightedLetters.M === 1) {
            goToNextStep();
          }
        }, 0);

        break;
      }

      case 5: {
        renderStep(() => {
          setMonsterState("sweating");
          showMonsterBubble(
            <>
              Bien ! Un nouveau bouton est apparu. Il permet de rinitialiser
              toutes les lettres de l'alphabet : c'est utile si vous pensez vous
              tre tromp quelque part&nbsp;!
            </>,
            true
          );

          setOnDocumentClick(() => goToNextStep);

          setDisabledInteractions({
            suggestWord: true,
            dragKeys: true,
          });
        }, 0);

        break;
      }

      case 6: {
        renderStep(() => {
          setMonsterState("speaking");
          showMonsterBubble(
            <>
              Bon, pour avancer il faut essayer un mot qui contient des lettres
              que nous n'avons pas encore testes&nbsp;!
              <br />
              Pourquoi pas le mot <b>DOIGT</b>&nbsp;?
            </>
          );

          setDisabledInteractions({
            suggestWord: false,
            dragKeys: false,
          });

          if (lastSuggestedWord === "DOIGT") {
            goToNextStep();
            setExpectedWord("");
          } else {
            setExpectedWord("DOIGT");
          }
        }, 0);

        break;
      }

      case 7: {
        renderStep(() => {
          setMonsterState("happy");
          showMonsterBubble(
            <>
              Wow, le mot <b>DOIGT</b> n'a aucune lettre en commun avec le
              mot-mystre&nbsp;! C'est une bonne nouvelle, vous pouvez marquer
              toutes les lettres du mot <b>DOIGT</b> en rouge !
            </>
          );

          setDisabledInteractions({
            suggestWord: true,
            dragKeys: false,
          });

          if (
            highlightedLetters.D === 0 &&
            highlightedLetters.O === 0 &&
            highlightedLetters.I === 0 &&
            highlightedLetters.G === 0 &&
            highlightedLetters.T === 0
          ) {
            goToNextStep();
          }
        });

        break;
      }

      case 8: {
        renderStep(() => {
          setMonsterState("thinking");
          showMonsterBubble(
            <>
              Trs bien ! Regardez les mots <b>MILLE</b> et <b>BILLE</b> que
              nous avons proposs plus tt&nbsp;: grce au mot <b>DOIGT</b> nous
              savons que la lettre <kbd>I</kbd> ne se trouve pas dans le
              mot-mystre. Il reste deux lettres  trouver dans ces mots, nous
              pouvons donc dduire que les lettres <kbd>L</kbd> et <kbd>E</kbd>{" "}
              sont prsentes. Marquez-les en vert&nbsp;!
            </>
          );

          setDisabledInteractions({
            suggestWord: true,
            dragKeys: false,
          });

          if (highlightedLetters.L === 1 && highlightedLetters.E === 1) {
            goToNextStep();
          }
        }, 0);

        break;
      }

      case 9: {
        renderStep(() => {
          setMonsterState("speaking");
          showMonsterBubble(
            <>
              Nous savons donc que les lettres <kbd>L</kbd>, <kbd>M</kbd> et
              <kbd>E</kbd> sont dans le mot-mystre. Nous devrions tester un mot
              qui les contient toutes&nbsp;! Essayons <b>LAMPE</b>.
            </>
          );

          setDisabledInteractions({
            suggestWord: false,
            dragKeys: false,
          });

          if (lastSuggestedWord === "LAMPE") {
            goToNextStep();
            setExpectedWord("");
          } else {
            setExpectedWord("LAMPE");
          }
        }, 0);

        break;
      }

      case 10: {
        renderStep(() => {
          setMonsterState("happy");
          showMonsterBubble(
            <>
              Oh ! Le mot <b>LAMPE</b> contient toutes les lettres du
              mot-mystre, quelle chance&nbsp;! Mais ce n'est <ins>pas</ins> le
              mot que nous devons trouver... Le mot-mystre est un anagramme de{" "}
              <b>LAMPE</b>.
            </>,
            true
          );

          setOnDocumentClick(() => goToNextStep);

          setDisabledInteractions({
            suggestWord: true,
            dragKeys: true,
          });
        });

        break;
      }

      case 11: {
        renderStep(() => {
          setMonsterState("speaking");
          showMonsterBubble(
            <>
              Comme vous avez trouv un anagramme, toutes les lettres du
              mot-mystre ont t dcouvertes&nbsp;! Elles ont automatiquement
              t mises en avant. Appuyez longuement sur les lettres pour les
              trier afin de vous aider  trouver le mot-mystre. Un bouton
              permet galement d'effectuer des mlanges pour vous&nbsp;!
            </>,
            true
          );

          setOnDocumentClick(() => goToNextStep);

          setDisabledInteractions({
            suggestWord: true,
            dragKeys: true,
          });
        }, 0);

        break;
      }

      case 12: {
        renderStep(() => {
          setMonsterState("speaking");
          showMonsterBubble(
            <>
              De plus, une lettre a t marque en bleue : cette lettre est{" "}
              <b>correctement positionne</b> et ne peut pas tre
              dplace&nbsp;!
              <br />
              Je vous laisse maintenant trouver le mot-mystre par vous-mme,
              bonne chance&nbsp;!
            </>,
            true
          );

          setOnDocumentClick(() => () => {
            setDisabledInteractions({
              suggestWord: false,
              dragKeys: false,
            });

            setOnDocumentClick(null);
            hideMonsterBubble();

            setMonsterState("walking");

            goToNextStep();
          });

          setDisabledInteractions({
            suggestWord: true,
            dragKeys: true,
          });
        }, 0);

        break;
      }
    }

    return () => {
      setOnDocumentClick(null);
      hideMonsterBubble();
      setMonsterState("walking");
    };
  }, [tutoStep, lastSuggestedWord, highlightedLetters, renderStep]);

  return (
    <TutorialContext.Provider value={{ expectedWord, disabledInteractions }}>
      {children}
    </TutorialContext.Provider>
  );
}

export function useTutorial() {
  return useContext(TutorialContext);
}
