import styled from "@mui/system/styled";

import Button from "../../common/Button";
import Gear from "../../common/icons/Gear";
import House from "../../common/icons/House";

import { useGameContext } from "../../game/GameProvider";

const Root = styled("nav")(({ theme }) => ({
  marginBottom: theme.spacing(1),
  display: "flex",
  justifyContent: "space-between",
  width: "100%",
}));

export default function Navigation() {
  const { gameState } = useGameContext();

  return (
    <Root>
      <Button
        component="a"
        href="#title"
        size="small"
        variant="secondary"
        aria-label="Accueil"
        icon={<House />}
      />

      <Button
        component="a"
        href={`#options?mode=${gameState.gameMode}`}
        size="small"
        variant="secondary"
        aria-label="Options"
        icon={<Gear />}
      />
    </Root>
  );
}
