import { getKey, storeKey } from ".";

export function getOptions() {
  return (
    getKey("options") || {
      music: 1,
      sounds: 1,
      backgroundEffectName: "none",
      backgroundBrightness: 1,
      keyboardLayout: "",
    }
  );
}

export function saveOption(optionName, optionValue) {
  const options = getOptions();

  storeKey("options", {
    ...options,
    [optionName]: optionValue,
  });
}
