import { useEffect } from "react";
import Box from "@mui/system/Box";
import Stack from "@mui/system/Stack";

import Button from "../common/Button";
import Panel from "../common/Panel";
import SectionContainer from "../common/SectionContainer";
import SlideUp from "../common/SlideUp";

import { useMainMonster } from "../monster/MonsterProvider";

export default function ErrorFallback() {
  const { setMonsterState, showMonsterBubble, hideMonsterBubble } =
    useMainMonster();

  useEffect(() => {
    setMonsterState("sweating");
    showMonsterBubble("Tout est cass, oh l l... !");

    return () => {
      setMonsterState("walking");
      hideMonsterBubble();
    };
  }, []);

  return (
    <Box margin="auto">
      <SectionContainer>
        <Box mx={1}>
          <Stack gap={5} mt={6} alignItems="center">
            <Panel
              title={<>Une erreur est survenue&nbsp;!</>}
              sx={{ textAlign: "center" }}
            />
            <Panel>
              Le problme ne vient pas de vous, rassurez-vous.
              <br /> <br />
              Cliquez sur le bouton ci-dessous pour revenir au menu principal du
              jeu.
              <br /> <br />
              Si le problme persiste, merci de contacter le dveloppeur du jeu
              !
            </Panel>
            <SlideUp delay={100}>
              <Button
                component="a"
                href="#title"
                variant="primary"
                size="small"
              >
                Revenir au menu
              </Button>
            </SlideUp>
          </Stack>
        </Box>
      </SectionContainer>
    </Box>
  );
}
