import { useState, useEffect, useRef } from "react";

import Box from "@mui/system/Box";
import styled from "@mui/system/styled";

import DownloadButton from "../../common/DownloadButton";
import Output from "../../common/form/Output";
import Panel from "../../common/Panel";

import { getTomorrowTimestamp } from "../../daily";

const Root = styled(Panel)(({ theme }) => ({
  padding: theme.spacing(6),
  paddingBottom: process.env.IS_SITE ? 0 : theme.spacing(6),
  margin: theme.spacing(3),
  textAlign: "center",
}));

function getStringifiedNextDailyTime() {
  const targetTimestamp = getTomorrowTimestamp();
  const now = new Date();

  const diff = targetTimestamp - now.getTime();

  if (diff <= 0 || diff >= 24 * 60 * 60 * 1000) {
    return false;
  }
  const diffDate = new Date(diff);

  const hours = String(diffDate.getHours()).padStart(2, "0");
  const minutes = String(diffDate.getMinutes()).padStart(2, "0");
  const seconds = String(diffDate.getSeconds()).padStart(2, "0");

  return `${hours}:${minutes}:${seconds}`;
}

export default function NextDailyPanel({ onEnd }) {
  const [nextDailyTime, setNextDailyTime] = useState("00:00:00");
  const nextDailyClock = useRef(null);

  const clearRenderDailyClock = () => {
    if (nextDailyClock.current) {
      window.clearInterval(nextDailyClock.current);
    }
  };

  useEffect(() => {
    clearRenderDailyClock();

    nextDailyClock.current = window.setInterval(() => {
      const newTime = getStringifiedNextDailyTime();

      if (newTime === false) {
        clearRenderDailyClock();
        onEnd();
      } else {
        setNextDailyTime(newTime);
      }
    });

    return clearRenderDailyClock;
  }, [onEnd]);

  return (
    <Root>
      <label htmlFor="daily-lock">Prochain mot du jour dans :</label>
      <Box mt={4}>
        <Output id="daily-clock">{nextDailyTime}</Output>
      </Box>

      {process.env.IS_SITE && (
        <p>
          Le mode "mot du jour" n'est disponible qu'une fois par jour.
          <br />
          Si vous souhaitez jouer plus, la version complte du jeu propose
          d'autres modes sans aucune limitation !<br />
          <br />
          <DownloadButton scale={0.5} />
        </p>
      )}
    </Root>
  );
}
