import Box from "@mui/system/Box";
import styled from "@mui/system/styled";
import useTheme from "@mui/system/useTheme";

import { useSounds } from "../sounds/SoundsProvider";

const BaseButton = styled(Box)(({ theme }) => ({
  ...theme.typography.buttonBase,
  display: "inline-flex",
  alignItems: "center",
  justifyContent: "center",
  userSelect: "none",
  textAlign: "center",
  color: theme.palette.text.main,
  borderStyle: "solid",
  borderColor: theme.palette.border.button,
  background: "var(--background)",
  cursor: "pointer",
  boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
  transition: "transform 0.1s ease-out, box-shadow 0.1s ease-out",
  "&:hover": {
    boxShadow: "0 6px 8px rgba(0, 0, 0, 0.45)",
    transform: "translateY(-2px)",
  },
  "&:active": {
    boxShadow: "0 2px 4px rgba(0, 0, 0, 0.3)",
    transform: "translateY(1px)",
  },
}));

const BigButton = styled(BaseButton)(({ theme }) => ({
  padding: theme.spacing(2, 4),
  borderWidth: 5,
  borderRadius: 20,
}));

const SmallButton = styled(BaseButton)(({ theme }) => ({
  ...theme.typography.buttonSmall,
  padding: theme.spacing(1, 2),
  borderWidth: 2,
  borderRadius: 10,
}));

const VerySmallButton = styled(SmallButton)(({ theme }) => ({
  ...theme.typography.buttonVerySmall,
  padding: theme.spacing(1),
}));

const IconContainer = styled("span")(() => ({
  display: "inline-block",
  width: 24,
  marginLeft: "auto",
}));

const sizeToComponent = {
  "very-small": VerySmallButton,
  small: SmallButton,
  big: BigButton,
};

export default function Button({
  component = "button",
  size = "big",
  variant = "primary",
  icon = null,
  children,
  onClick,
  disabled = false,
  ...otherProps
}) {
  const { palette } = useTheme();
  const { playSound } = useSounds();

  const RootButton = sizeToComponent[size];

  const extraStyle = disabled
    ? {
        cursor: "not-allowed",
        filter: "grayscale(1)",
        opacity: "0.85",
        pointerEvents: "none",
      }
    : {};

  return (
    <RootButton
      component={component}
      style={{
        "--background": disabled
          ? palette.button.secondary
          : palette.button[variant],
        ...extraStyle,
      }}
      tabIndex={disabled ? -1 : 1}
      {...otherProps}
      onClick={
        disabled
          ? (e) => e.preventDefault()
          : (e) => {
              playSound("click");
              onClick?.(e);
            }
      }
    >
      {children}
      {icon && <IconContainer>{icon}</IconContainer>}
    </RootButton>
  );
}
