export function getMonsterColor(id) {
  return colors.find((color) => color.id === id);
}

export function getMonsterColorByAchievementId(id) {
  const idToCheck = id === "first-word" ? null : id;
  return colors.find((color) => color.achievementId === idToCheck);
}
