import styled from "@mui/system/styled";

import Input from "./Input";

const Root = styled(Input)(({ theme }) => ({
  ...theme.typography.panelText,
  appearance: "none",
  display: "block",
  width: "100%",
  verticalAlign: "middle",
  textTransform: "none",
  maxWidth: "none",
  boxSizing: "border-box",
}));

export default function Select(props) {
  return <Root as="select" {...props} />;
}
