const STORAGE_KEY = "motigame-save";

function getSave() {
  const save = localStorage.getItem(STORAGE_KEY);

  try {
    return JSON.parse(save) || {};
  } catch (e) {
    return {};
  }
}

function storeNewSave(save) {
  localStorage.setItem(STORAGE_KEY, JSON.stringify(save));
}

export function storeKey(key, value) {
  const save = getSave();

  storeNewSave({
    ...save,
    [key]: value,
  });
}

export function getKey(key) {
  const save = getSave();

  return save[key];
}

export function removeKey(key) {
  const save = getSave();
  delete save[key];

  storeNewSave(save);
}
