import { useEffect, useState, useRef } from "react";

import styled from "@mui/system/styled";

import Panel, { Content as PanelContent } from "../common/Panel";

import { hasUnlockedAchievement } from "../save/achievements";
import { getAchievement, achievements } from ".";

const AchievementBase = styled(Panel)(({ theme }) => ({
  backgroundColor: "transparent",
  display: "flex",
  minWidth: 300,
  padding: theme.spacing(2, 1),
  overflow: "visible",
}));

const AchievementPopover = styled(AchievementBase)(({ theme }) => ({
  "--y": 0,
  position: "fixed",
  inset: "unset",
  right: theme.spacing(2),
  top: `calc(${theme.spacing(2)} + var(--y))`,
  cursor: "pointer",
  transition:
    "top 333ms ease-in-out, transform 333ms ease-in-out, opacity 333ms ease-in-out",
  borderTopLeftRadius: 0,
  zIndex: theme.zIndex.popover,
  transform: "translateX(200px)",
  opacity: 0,

  "&.shown": {
    transform: "translateX(0)",
    opacity: 1,
  },
}));

const AchievementContent = styled(PanelContent)(() => ({
  display: "flex",
  flexDirection: "row",
  padding: 0,
}));

const AchievementImage = styled("div")(({ theme }) => ({
  "--sprite-position-index": 0,
  "--scale": "calc(512 / 48)",
  "--total-achievements": achievements.length,
  backgroundSize:
    "calc(calc(512px * var(--total-achievements)) / var(--scale)) calc(512px / var(--scale))",
  flexShrink: 0,
  display: "block",
  textAlign: "center",
  fontSize: "2rem",
  width: "3rem",
  height: "3rem",
  backgroundColor: "#fff",
  border: `1px solid ${theme.palette.shadow.panel}`,
  borderRadius: 3,
  marginLeft: theme.spacing(1),
  zIndex: theme.zIndex.neutral,
  backgroundImage: 'url("./images/achievements.png")',
  backgroundPosition: "calc(-48px * var(--sprite-position-index)) 0",
}));

const AchievementUnknownIcon = styled("span")(({ theme }) => ({
  fontFamily: `"Baloo 2", "Comic Sans MS"`,
  textAlign: "center",
  fontWeight: "bold",
  color: theme.palette.text.main,
}));

const AchievementText = styled("div")(({ theme }) => ({
  padding: theme.spacing(1),
  marginLeft: theme.spacing(1),
  alignSelf: "center",
}));

const AchievementName = styled("span")(({ theme }) => ({
  "--shadowColor": theme.palette.shadow.panel,
  fontWeight: "bold",
  fontSize: "1rem",
  textShadow: `1px 1px var(--shadowColor), -1px 1px var(--shadowColor),
    -1px -1px var(--shadowColor), 1px -1px var(--shadowColor)`,
}));

const AchievementDescription = styled("p")(() => ({
  margin: 0,
  fontSize: "0.75rem",
}));

export default function Achievement({
  id,
  popoverTimeout = 0,
  style = {},
  onPopoverEnd,
  onClick,
}) {
  const clockClose = useRef(null);
  const [isClosed, setIsClosed] = useState(popoverTimeout > 0);
  const { name, description, spritePositionIndex } = getAchievement(id);
  const isLocked = !hasUnlockedAchievement(id);
  const isPopover = Boolean(popoverTimeout);

  const Root = isPopover ? AchievementPopover : AchievementBase;

  const close = () => {
    setIsClosed(true);

    if (clockClose.current) {
      window.clearTimeout(clockClose.current);
    }

    clockClose.current = window.setTimeout(onPopoverEnd, 333);
  };

  useEffect(() => {
    if (popoverTimeout < 1) {
      return;
    }

    window.requestAnimationFrame(() => {
      setIsClosed(false);

      clockClose.current = window.setTimeout(close, popoverTimeout);
    });

    return () => {
      if (clockClose.current) {
        window.clearTimeout(clockClose.current);
      }
    };
  }, []);

  return (
    <Root
      className={isClosed ? "" : "shown"}
      style={{
        filter: isLocked ? "grayscale(1)" : "none",
        ...style,
      }}
      contentComponent={AchievementContent}
      onClick={isPopover ? close : isLocked ? null : onClick}
    >
      <AchievementImage
        style={
          isLocked
            ? { backgroundImage: "none" }
            : { "--sprite-position-index": spritePositionIndex }
        }
      >
        {isLocked && <AchievementUnknownIcon>?</AchievementUnknownIcon>}
      </AchievementImage>

      <AchievementText>
        <AchievementName dangerouslySetInnerHTML={{ __html: name }} />
        <AchievementDescription
          dangerouslySetInnerHTML={{ __html: isLocked ? "???" : description }}
        />
      </AchievementText>
    </Root>
  );
}
