import { useState } from "react";

import Box from "@mui/system/Box";
import styled from "@mui/system/styled";

const Button = styled("button")({
  appearance: "none",
  border: 0,
  color: "inherit",
  background: "transparent",
  height: "32px",
  width: "100%",
  display: "inline-flex",
  flexDirection: "row",
  alignItems: "center",
  justifyContent: "center",
  cursor: "pointer",
});

const Label = styled("span")({
  textDecoration: "underline",
  fontSize: "1.2rem",
});

const Arrow = styled("span")(({ theme }) => ({
  marginLeft: theme.spacing(2),
  transition: "transform 333ms ease-in-out",
}));

const HideableContainer = styled(Box)({
  overflow: "hidden",
  transition: "max-height 333ms ease-in-out",
});

export default function ShowHide({ defaultValue = "hide", label, children }) {
  const [isShown, setIsShown] = useState(defaultValue !== "hide");

  return (
    <>
      <Button type="button" onClick={() => setIsShown((prev) => !prev)}>
        <Label> {label}</Label>

        <Arrow sx={{ transform: `rotate(${isShown ? "180deg" : 0})` }}></Arrow>
      </Button>
      <HideableContainer sx={{ maxHeight: isShown ? 999 : 0 }}>
        {children}
      </HideableContainer>
    </>
  );
}
