import styled from "@mui/system/styled";

const Root = styled("a")(() => ({
  "--scale": 1,
  display: "inline-block",
  width: `calc(270px * var(--scale))`,
  height: `calc(80px * var(--scale))`,
  backgroundImage: "url(./images/download-on-gp.png)",
  backgroundSize: "contain",
}));

export default function DownloadButton({ scale = 1, ...otherProps }) {
  return (
    <Root
      aria-label="Tlcharger Motigma sur Google Play"
      style={{ "--scale": scale }}
      href="https://play.google.com/store/apps/details?id=com.agueret.motigma"
      target="_blank"
      {...otherProps}
    />
  );
}
