import { useCallback } from "react";

import styled from "@mui/system/styled";

import { useGameContext } from "../../game/GameProvider";

const DropzoneContainer = styled("div")(({ theme }) => ({
  position: "fixed",
  inset: 0,
  display: "flex",
  zIndex: theme.zIndex.letterDropzone,
}));

const Dropzone = styled("div")(() => ({
  opacity: 0.4,
  flex: 1,
  transition: "opacity 1s ease-out",
  touchAction: "none",
}));

const DropzoneNotOK = styled(Dropzone)(() => ({
  backgroundImage: "linear-gradient(90deg, red, transparent)",
}));

const DropzoneNeutral = styled(Dropzone)(() => ({
  backgroundImage:
    "linear-gradient(90deg, transparent, #b5b5b5, #b5b5b5, #b5b5b5, transparent)",
}));

const DropzoneOK = styled(Dropzone)(() => ({
  backgroundImage: "linear-gradient(90deg, transparent, green)",
}));

export default function LetterDropzone({ letter, onZoneHover }) {
  const { dispatch } = useGameContext();

  const getIncreaseOpacity = (value) => (e) => {
    e.target.style.opacity = 0.9;
    onZoneHover(value);
  };

  const decreaseOpacity = (e) => {
    e.target.style.removeProperty("opacity");
  };

  const getHighlightLetter = useCallback(
    (value) => () => {
      if (value === null) {
        dispatch({ type: "unhighlightLetter", payload: { letter } });
      } else {
        dispatch({ type: "highlightLetter", payload: { letter, value } });
      }
    },
    [letter]
  );

  return (
    <DropzoneContainer>
      <DropzoneNotOK
        onPointerEnter={getIncreaseOpacity(0)}
        onPointerLeave={decreaseOpacity}
        onPointerUp={getHighlightLetter(0)}
      />
      <DropzoneNeutral
        onPointerEnter={getIncreaseOpacity(null)}
        onPointerLeave={decreaseOpacity}
        onPointerUp={getHighlightLetter(null)}
      />
      <DropzoneOK
        onPointerEnter={getIncreaseOpacity(1)}
        onPointerLeave={decreaseOpacity}
        onPointerUp={getHighlightLetter(1)}
      />
    </DropzoneContainer>
  );
}
