import { useEffect, useState } from "react";

import styled from "@mui/system/styled";

import { useMainMonster } from "../../monster/MonsterProvider";
import { useCurrentSection } from "../../sections/SectionsProvider";

import { Word, Letter, Counter } from "../game/WordList";

const ExampleWord = styled(Word)(({ theme }) => ({
  display: "inline-flex",
  margin: theme.spacing(3, 0, 1, 3),
}));

export default function Explanations() {
  const [tutoStep, setTutoStep] = useState(1);

  const { setMonsterState, showMonsterBubble, hideMonsterBubble } =
    useMainMonster();
  const { goToSection } = useCurrentSection();

  useEffect(() => {
    let onDocumentClick = null;

    switch (tutoStep) {
      case 1: {
        setMonsterState("happy");
        showMonsterBubble(
          <>
            Dcouvrons comment jouer  <strong>Motigma</strong>&nbsp;!
          </>,
          true
        );
        onDocumentClick = () => setTutoStep(2);
        break;
      }

      case 2: {
        setMonsterState("speaking");
        showMonsterBubble(
          <>
            Dans ce jeu, vous devez trouver un mot-mystre. Pour chacune de vos
            propositions, le jeu vous indiquera le nombre de lettres qu'elle a
            en <b>commun</b> avec le mot  deviner.
          </>,
          true
        );
        onDocumentClick = () => setTutoStep(3);
        break;
      }

      case 3: {
        setMonsterState("speaking");
        showMonsterBubble(
          <>
            Par exemple, si le mot-mystre est <strong>MARIN</strong> et que
            vous tentez le mot <strong>AMOUR</strong>, le jeu indiquera qu'il y
            a 3 lettres en commun <ins>sans vous dire lesquelles</ins>&nbsp;:
            <br />
            <ExampleWord as="span">
              <Letter>m</Letter>
              <Letter>a</Letter>
              <Letter>r</Letter>
              <Letter>i</Letter>
              <Letter>n</Letter>
              <Counter>3</Counter>
            </ExampleWord>
          </>,
          true
        );
        onDocumentClick = () => setTutoStep(4);
        break;
      }

      case 4: {
        setMonsterState("happy");
        showMonsterBubble(
          <>
            Jouons une partie ensemble pour que vous compreniez bien le
            principe&nbsp;!
          </>,
          true
        );
        onDocumentClick = () => goToSection("game", { mode: "tuto" });
        break;
      }
    }

    if (onDocumentClick) {
      document.body.addEventListener("click", onDocumentClick, { once: true });
    }

    return () => {
      hideMonsterBubble();
      setMonsterState("walking");

      if (onDocumentClick) {
        document.body.removeEventListener("click", onDocumentClick);
      }
    };
  }, [tutoStep]);

  return null;
}
