import { achievements } from "../achievements";
import { getKey, storeKey } from ".";

function getUnlockedAchievements() {
  return getKey("achievements") || [];
}

export function hasUnlockedAtLeastOneAchievement() {
  return getUnlockedAchievements().length >= 1;
}

export function hasUnlockedAllAchievements() {
  return process.env.IS_SITE
    ? hasUnlockedAtLeastOneAchievement()
    : getUnlockedAchievements().length === achievements.length;
}

export function hasUnlockedAchievement(id) {
  return getUnlockedAchievements().includes(id);
}

export function unlockAchievement(id) {
  if (process.env.IS_SITE && id !== "pock-monster") {
    return false;
  }

  if (hasUnlockedAchievement(id)) {
    return false;
  }

  storeKey("achievements", [...getUnlockedAchievements(), id]);

  return true;
}
