import { useEffect } from "react";

import Button from "../../common/Button";
import Link from "../../common/Link";
import SectionContainer from "../../common/SectionContainer";

import { useMainMonster } from "../../monster/MonsterProvider";
import Panel from "../../common/Panel";

export default function Policies() {
  const { setMonsterState, showMonsterBubble, hideMonsterBubble } =
    useMainMonster();

  useEffect(() => {
    setMonsterState("sweating");
    showMonsterBubble(
      <>Vous n'avez pas rellement tout lu, n'est-ce pas&nbsp;?</>
    );

    return () => {
      setMonsterState("walking");
      hideMonsterBubble();
    };
  }, [setMonsterState, showMonsterBubble, hideMonsterBubble]);

  return (
    <SectionContainer>
      <Panel title="Politique de Confidentialit" sx={{ maxWidth: 500 }}>
        <p>
          Cette application respecte la confidentialit des utilisateurs. Aucune
          donne personnelle n'est collecte, stocke ou partage lors de
          l'utilisation de cette application.
        </p>
      </Panel>

      <Panel title="Collecte de Donnes" sx={{ maxWidth: 500 }}>
        <p>
          Cette application ne recueille aucune information personnelle
          identifiable. Elle ne collecte ni n'utilise de donnes telles que des
          informations de localisation, des informations financires, ou tout
          autre type de donnes utilisateur.
        </p>
      </Panel>

      <Panel title="Utilisation de l'Application" sx={{ maxWidth: 500 }}>
        <p>
          L'application peut tre utilise sans aucune proccupation concernant
          la collecte ou le partage de donnes personnelles.
        </p>
      </Panel>

      <Panel title="Modifications" sx={{ maxWidth: 500 }}>
        <p>
          Cette politique de confidentialit peut tre mise  jour
          occasionnellement. Toute modification sera publie sur cette page. Il
          est recommand de consulter rgulirement cette page pour rester
          inform des ventuelles modifications.
        </p>
      </Panel>

      <Panel title="Contact" sx={{ maxWidth: 500 }}>
        <p>
          Pour toute question relative  cette politique de confidentialit,
          veuillez vous adresser  l'adresse suivante :{" "}
          <Link href="mailto:adrien.grt@gmail.com">adrien.grt@gmail.com</Link>.
        </p>
      </Panel>

      <Button
        variant="primary"
        size="small"
        component="a"
        href={process.env.IS_SITE ? "#splashscreen" : "#title"}
        sx={{ alignSelf: "center" }}
      >
        Retour
      </Button>

      <br />
    </SectionContainer>
  );
}
