import { useState, useEffect } from "react";

import ThemeProvider from "@mui/system/ThemeProvider";
import GlobalStyles from "@mui/system/GlobalStyles";

import AchievementsProvider from "../achievements/AchievementsProvider";
import ErrorBoundary from "../errors/ErrorBoundary";
import MonsterProvider from "../monster/MonsterProvider";
import OptionsProvider from "../options/OptionsProvider";
import SoundsProvider from "../sounds/SoundsProvider";
import getKeyboardLayout from "../options/keyboard";
import Sections from "../sections";

import BackgroundStyle from "./BackgroundStyle";
import BottomScene from "./BottomScene";
import theme from "./theme";

export default function App() {
  const [defaultKeyboardLayout, setDefaultKeyboardLayout] = useState(null);

  useEffect(() => {
    getKeyboardLayout().then(setDefaultKeyboardLayout);
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyles
        styles={(theme) => ({
          body: {
            backgroundColor: theme.palette.background.main,
            ...theme.typography.main,
          },
          "::view-transition-group(disabled),::view-transition-old(disabled),::view-transition-new(disabled)":
            {
              animation: "none",
              animationDuration: 0,
            },
        })}
      />

      {defaultKeyboardLayout && (
        <OptionsProvider defaultKeyboardLayout={defaultKeyboardLayout}>
          <BackgroundStyle />
          <SoundsProvider>
            <AchievementsProvider>
              <MonsterProvider>
                <Sections sectionWrapper={ErrorBoundary} />
                <BottomScene />
              </MonsterProvider>
            </AchievementsProvider>
          </SoundsProvider>
        </OptionsProvider>
      )}
    </ThemeProvider>
  );
}
