import {
  useState,
  useEffect,
  createContext,
  useContext,
  useCallback,
  Fragment,
} from "react";
import { flushSync } from "react-dom";

const fallbackSectionName = "title";

const SectionsContext = createContext({
  currentSectionName: fallbackSectionName,
  currentSectionVars: new URLSearchParams(),
  goToSection: () => {},
});

export default function SectionsProvider({
  defaultSection = fallbackSectionName,
  defaultVars = {},
  scrollContainer = window,
  wrapper: Wrapper = Fragment,
  sections,
}) {
  const [currentSectionName, setCurrentSectionName] = useState(() => {
    window.requestAnimationFrame(() => {
      goToSection(defaultSection, defaultVars);
      onHashChange();
    });
    return defaultSection;
  });

  const currentSection =
    sections.find(({ name }) => name === currentSectionName) || {};

  const possibleNextSections = currentSection.possibleNextSections || null;

  const [currentSectionVars, setCurrentSectionVars] = useState(
    new URLSearchParams()
  );

  const [previousSectionName, setPreviousSectionName] = useState(null);

  const isValidSectionName = (sectionName) =>
    sections.some(({ name }) => name === sectionName);

  const renderSection = useCallback(
    (nextSectionName, vars) => {
      if (
        Boolean(possibleNextSections) &&
        !possibleNextSections.includes(nextSectionName)
      ) {
        window.history.forward();
        return;
      }

      if (!isValidSectionName(nextSectionName)) {
        goToSection(fallbackSectionName);
        return;
      }

      if (currentSection.enableViewTransitions) {
        document.startViewTransition(() => {
          flushSync(() => {
            setPreviousSectionName(currentSectionName);
            setCurrentSectionVars(vars);
            setCurrentSectionName(nextSectionName);
          });
        });
      } else {
        setPreviousSectionName(currentSectionName);
        setCurrentSectionVars(vars);
        setCurrentSectionName(nextSectionName);
      }
    },
    [currentSectionName, previousSectionName]
  );

  const onHashChange = useCallback(
    (e) => {
      e?.stopPropagation();
      const [sectionName, vars] = window.location.hash.substring(1).split("?");

      renderSection(sectionName, new URLSearchParams(vars));
    },
    [renderSection]
  );

  useEffect(() => {
    window.addEventListener("hashchange", onHashChange);

    return () => window.removeEventListener("hashchange", onHashChange);
  }, [onHashChange]);

  useEffect(() => {
    scrollContainer.scrollTo(0, 0);
  }, [currentSectionName, scrollContainer]);

  function goToSection(targetSectionName, vars = {}) {
    const params = new URLSearchParams(vars);

    window.location.hash = `${targetSectionName}${
      params.size > 0 ? "?" + params.toString() : ""
    }`;
  }

  return (
    <SectionsContext.Provider
      value={{ currentSectionName, currentSectionVars, goToSection }}
    >
      <Wrapper key={currentSectionName}>{currentSection?.element}</Wrapper>
    </SectionsContext.Provider>
  );
}

export function useCurrentSection() {
  return useContext(SectionsContext);
}
