import styled from "@mui/system/styled";

const Root = styled("div")(({ theme }) => ({
  display: "inline-block",
  backgroundColor: theme.palette.background.bubble,
  padding: theme.spacing(2),
  zIndex: theme.zIndex.bubble,
  borderRadius: 20,
  position: "relative",
  filter: "drop-shadow(1px 1px 0px #000)",
  textAlign: "center",

  "&.click-to-continue": {
    paddingBottom: "22px",

    "&:after": {
      content: "''",
      display: "block",
      width: 0,
      height: 0,
      border: "0.5rem solid transparent",
      borderLeftColor: theme.palette.text.main,
      position: "absolute",
      right: theme.spacing(2),
      bottom: theme.spacing(1),
      animation: "blink 1s infinite step-start",

      "@keyframes blink": {
        "50%": {
          opacity: 0,
        },
      },
    },
  },

  "&:before": {
    content: "''",
    position: "absolute",
    left: "calc(50% - 10px)",
    width: 0,
    height: 0,
    borderRight: "10px solid transparent",
    borderTop: 0,
    borderBottom: 0,
    borderLeft: "10px solid transparent",
  },
  "&.arrow-bottom:before": {
    borderTop: `20px solid ${theme.palette.background.bubble}`,
    bottom: -15,
  },
  "&.arrow-top:before": {
    borderBottom: `20px solid ${theme.palette.background.bubble}`,
    top: -15,
  },
}));

export default function Bubble({
  children,
  arrowPosition = "bottom",
  showClickToContinue = false,
  ...otherProps
}) {
  return (
    <Root
      {...otherProps}
      className={`arrow-${arrowPosition} ${
        showClickToContinue ? "click-to-continue" : ""
      } ${otherProps.className ?? ""}`}
    >
      {children}
    </Root>
  );
}
