import { isValidWord } from "../words";

export const isLetterLocked = (gameState, letter) =>
  gameState.lockedLetters?.includes(letter) || false;

export const getLetterIndex = (gameState, letter) => {
  if (isLetterLocked(gameState, letter)) {
    return gameState.wordToGuess.indexOf(letter);
  }

  return "?";
};

export const getLetterClassname = (gameState, letter, indexToCheck = null) => {
  if (isLetterLocked(gameState, letter)) {
    return indexToCheck === null ||
      indexToCheck === getLetterIndex(gameState, letter)
      ? "is-locked"
      : "is-ok";
  }

  if (letter in gameState.highlightedLetters) {
    return gameState.highlightedLetters[letter] === 1 ? "is-ok" : "is-not-ok";
  }

  return "";
};

export const hasLockedLetters = (gameState) =>
  gameState.lockedLetters.length > 0;

export const getValidTryCount = (gameState) =>
  gameState?.suggestedWords?.filter(isValidWord).length ?? 0;
