import { useEffect } from "react";

import styled from "@mui/system/styled";

import ConstraintButton from "../../common/ConstraintButton";
import Logo from "../../common/Logo";
import SectionContainer from "../../common/SectionContainer";

import { useMainMonster } from "../../monster/MonsterProvider";
import { useOptions } from "../../options/OptionsProvider";
import { useCurrentSection } from "../../sections/SectionsProvider";
import { useSounds } from "../../sounds/SoundsProvider";

import {
  hasAlreadyOpenedApp,
  saveLastSeenVersionAsCurrent,
  getLastSeenVersion,
} from "../../save/lastSeenVersion";
import { getReleaseNotesOfCurrentVersion } from "../../releaseNotes";

const MainLogo = styled(Logo)(({ theme }) => ({
  width: "70dvw",
  height: "50dvh",
  marginTop: theme.spacing(1),
  marginBottom: theme.spacing(4),
}));

export default function Splaschreen() {
  const { setMonsterState } = useMainMonster();
  const { goToSection } = useCurrentSection();
  const { options } = useOptions();
  const { initBackgroundMusic, playMusic } = useSounds();

  const nextSection = hasAlreadyOpenedApp()
    ? getLastSeenVersion() === App.version || !getReleaseNotesOfCurrentVersion()
      ? "title"
      : "news"
    : "welcome";

  useEffect(() => {
    setMonsterState("hidden");

    return () => setMonsterState("walking");
  }, [setMonsterState]);

  const goToNextPage = (e) => {
    e.preventDefault();
    initBackgroundMusic(options.music).then(playMusic);
    saveLastSeenVersionAsCurrent();
    goToSection(nextSection);
  };

  return (
    <SectionContainer>
      <MainLogo />

      <ConstraintButton
        href={`#${nextSection}`}
        onClick={goToNextPage}
        size="big"
        sx={{ viewTransitionName: "main-button" }}
      >
        Commencer
      </ConstraintButton>
    </SectionContainer>
  );
}
