import styled from "@mui/system/styled";

const Root = styled("div")(({ theme }) => ({
  borderImage: "url(./images/panel.png)",
  borderImageRepeat: "round",
  borderImageWidth: "33px",
  borderImageSlice: "33 fill",
  padding: theme.spacing(2),
  margin: 0,
}));

const Title = styled("h2")(({ theme }) => ({
  ...theme.typography.panelTitle,
  padding: 0,
  margin: 0,
  width: "100%",
  "--shadowColor": theme.palette.shadow.panel,
  color: theme.palette.text.panel,
  textShadow:
    "1px 1px var(--shadowColor), -1px 1px var(--shadowColor), -1px -1px var(--shadowColor), 1px -1px var(--shadowColor)",
}));

export const Content = styled("div")(({ theme }) => ({
  ...theme.typography.panelText,
  color: theme.palette.text.panel,
  padding: theme.spacing(0, 2),
}));

export default function Panel({
  children,
  title,
  contentComponent: ContentComponent = Content,
  ...otherProps
}) {
  return (
    <Root {...otherProps}>
      {title && <Title>{title}</Title>}
      {children && <ContentComponent>{children}</ContentComponent>}
    </Root>
  );
}
