import { useEffect } from "react";

import Stack from "@mui/system/Stack";
import styled from "@mui/system/styled";

import ConstraintButton from "../../common/ConstraintButton";
import List from "../../common/List";
import Panel from "../../common/Panel";
import SectionContainer from "../../common/SectionContainer";

import { useMainMonster } from "../../monster/MonsterProvider";
import { getReleaseNotesOfCurrentVersion } from "../../releaseNotes";

const Root = styled("div")(({ theme }) => ({
  margin: "auto",
  width: `calc(100% - ${theme.spacing(1)})`,
  maxWidth: 768,
  boxSizing: "border-box",
}));

export default function News() {
  const { setMonsterState } = useMainMonster();

  useEffect(() => {
    setMonsterState("happy");

    return () => setMonsterState("walking");
  });

  return (
    <Root>
      <SectionContainer>
        <Stack mx={1} my={5} mb={10} gap={3}>
          <Panel
            title={<>Motigma a t mis  jour&nbsp;!</>}
            sx={{ textAlign: "center", alignSelf: "center" }}
          >
            Dcouvrez ce qu'apporte la version <b>{App.version}</b>.
          </Panel>

          <Panel>
            <List>{getReleaseNotesOfCurrentVersion()}</List>
          </Panel>

          <ConstraintButton
            href="#title"
            sx={{ viewTransitionName: "main-button" }}
          >
            OK !
          </ConstraintButton>
        </Stack>
      </SectionContainer>
    </Root>
  );
}
