import { useCallback, useState } from "react";

import styled from "@mui/system/styled";

import Bubble from "../monster/Bubble";
import Monster from "../monster/Monster";
import { useAchievements } from "../achievements/AchievementsProvider";
import { useMainMonster } from "../monster/MonsterProvider";
import { useSounds } from "../sounds/SoundsProvider";

const Root = styled("aside")(() => ({
  position: "relative",
  content: "''",
  justifySelf: "stretch",
  marginTop: "auto",
  paddingBottom: 32,
  display: "flex",
  alignItems: "center",
  justifyContent: "space-between",
  flexDirection: "column",
  backgroundPosition: "bottom",
  backgroundRepeat: "repeat-x",
  backgroundImage: 'url("./images/ground.png")',
  transformOrigin: "bottom center",
  overflowX: "clip",
  gap: "54px",
}));

const MonsterBubble = styled(Bubble)(({ theme }) => ({
  margin: theme.spacing(0, 2),
}));

const MainMonster = styled(Monster)({
  viewTransitionName: "disabled",
});

export default function BottomScene() {
  const [isMonsterGoingToLeft, setIsMonsterGoingToLeft] = useState(false);
  const { monsterState, setMonsterState, monsterBubble } = useMainMonster();
  const { playSound } = useSounds();
  const { showAchievement } = useAchievements();

  const onMonsterClick = useCallback(() => {
    if (
      monsterState.includes("shocked") ||
      ["#news", "#explanations", "#game?mode=tuto"].includes(
        window.location.hash
      )
    ) {
      return;
    }

    const wasWalking = monsterState.includes("walking");
    const wasFromMiddle = monsterState.includes("from-middle");
    setMonsterState(
      `shocked with-exclamation ${wasWalking ? "walking" : ""} ${
        wasFromMiddle ? "from-middle" : ""
      }`
    );

    showAchievement("pock-monster");

    playSound("surprised");

    window.setTimeout(() => {
      setMonsterState(monsterState);
    }, 1000);
  }, [monsterState, playSound]);

  const onMonsterAnimationEnd = () => {
    if (monsterState.includes("walking")) {
      setIsMonsterGoingToLeft((prev) => !prev);
      setMonsterState("walking");
    }
  };

  return (
    <Root>
      {Boolean(monsterBubble?.message) && (
        <MonsterBubble
          arrowPosition="bottom"
          showClickToContinue={monsterBubble.showClickToContinue}
        >
          {monsterBubble.message}
        </MonsterBubble>
      )}
      <MainMonster
        state={monsterState}
        isGoingToLeft={isMonsterGoingToLeft}
        onClick={onMonsterClick}
        onAnimationEnd={onMonsterAnimationEnd}
      />
    </Root>
  );
}
