import { fontStyle } from "@mui/system";
import createTheme from "@mui/system/createTheme";

const buttonTypography = {
  fontFamily: `"Baloo 2", "Comic Sans MS"`,
  fontSize: "2rem",
  fontWeight: "bold",
  textTransform: "uppercase",
  textDecoration: "none",
  textShadow: "1px 1px white, -1px 1px white, -1px -1px white, 1px -1px white",
};

export default createTheme({
  spacing: (value) => value * 5,
  zIndex: {
    neutral: 1,
    keyboardRow: 10,
    letterDropzone: 15,
    errorMessage: 50,
    bubble: 100,
    monster: 150,
    popover: 200,
    draggedElement: 300,
    aboveAll: 999,
  },
  typography: {
    main: {
      fontFamily: "Arial, sans-serif",
      fontSize: "20px",
    },
    buttonBase: buttonTypography,
    buttonSmall: {
      ...buttonTypography,
      fontSize: "1.5rem",
    },
    buttonVerySmall: {
      ...buttonTypography,
      fontSize: "1rem",
    },
    panelTitle: {
      fontFamily: "Arial, sans-serif",
      fontSize: "1.75rem",
    },
    panelText: {
      fontFamily: "Arial, sans-serif",
      fontSize: "1rem",
      textShadow: "0px 1px #604831",
    },
    word: {
      fontFamily: "monospace",
      textTransform: "uppercase",
      fontSize: "1.25rem",
      textShadow: "none",
    },
  },
  palette: {
    text: {
      disabled: "#a4a4a4",
      main: "#000",
      panel: "#ffdbb6",
      counter: "#fff",
    },
    background: {
      main: "#7dd2c3",
      panel: "#604831",
      bubble: "#fff",
      validWord: "#fff",
      nonValidWord: "#000",
      counter: "#000",
      input: "#fff",
    },
    border: {
      button: "#131313",
    },
    button: {
      primary: "linear-gradient(170deg, #f54e1c, #fd7614, #b6016c)",
      secondary: "linear-gradient(170deg, #47e54d, #00a2ae, #005ce5)",
      tertiary: "linear-gradient(170deg, #cfff3e, #fff200, #f5ae0a)",
      danger: "linear-gradient(170deg, #ebbdbd, #ca3535, #bf0000)",
    },
    shadow: {
      panel: "#604831",
      input: "#b08a63",
    },
  },
});
