import GlobalStyles from "@mui/system/GlobalStyles";

import { useOptions } from "../options/OptionsProvider";

export default function BackgroundStyle() {
  const { options } = useOptions();

  const effectName = options.backgroundEffectName;
  const brightness = options.backgroundBrightness;

  let filter = "none";

  if (effectName === "none") {
    if (brightness === 1) {
      filter = "none";
    } else {
      filter = `brightness(${brightness})`;
    }
  } else {
    filter = `${effectName}(1) brightness(${brightness})`;
  }

  return (
    <GlobalStyles
      styles={() => ({
        "#background": {
          "--filter": filter,
        },
      })}
    />
  );
}
